import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavigationService } from '../../services/navigation.service';
import { SharedDataService } from '../../services/sharedData.service';
import { TranslateService } from '@ngx-translate/core';
import { TabIds } from './tab-ids';
import { Subscription } from '../../../../../node_modules/rxjs/Subscription';
import { WebcomponentApiService } from '../../services/webcomponent-api.service';
import {FeatureToggleService} from '../../directives/feature-toggle/feature-toggle.service';

@Component({
  selector: 'app-router-outlet',
  templateUrl: './router-outlet.component.html',
  styleUrls: ['./router-outlet.component.scss']
})
export class RouterOutletComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  isSearchTermPresent = false;
  readonly tabIds = TabIds;
  componentId: string;

  constructor(private readonly activatedRoute: ActivatedRoute,
              private readonly router: Router,
              private readonly sharedDataSvc: SharedDataService,
              private readonly navigationSvc: NavigationService,
              private readonly webcomponentApiSvc: WebcomponentApiService, ) {
  }

  ngOnInit() {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.checkSearchTermPresent(queryParams);
    });

    this.activatedRoute.url.subscribe(() => {
      const id = this.activatedRoute.routeConfig.data.key;

      if (id !== this.componentId) {
        this.componentId = id;
        this.sharedDataSvc.activate(id);
        this.webcomponentApiSvc.queryParamsChanged(this.navigationSvc.getRawQueryParams());
      }

      const queryParams = this.navigationSvc.getQueryParams();
      this.checkSearchTermPresent(queryParams);
    });
  }

  private checkSearchTermPresent(queryParams) {
    this.isSearchTermPresent = typeof queryParams['searchTerm'] === 'string';
  }

  isArchiveDetailPage(): boolean {
      return this.componentId === TabIds.ARCHIVE_DETAILS || this.componentId === TabIds.ARCHIVE_DETAILS_HUB;
  }

  getCustomRoutes() {
    return this.router.config.filter(route => route.data && route.data.isCustom);
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
