import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { ManifestParams } from '../../shared/services/api.service';
import { ImagesTabService } from '../images-tab.service';
import { ClientConfigurationService } from '../../shared/services/client-configuration.service';
import queryStringParser from 'query-string';

@Component({
  selector: 'app-image-details',
  templateUrl: './image-details.component.html',
  styleUrls: ['./image-details.component.scss']
})
export class ImageDetailsComponent implements OnInit, OnDestroy {

  pageTitle: string;
  routeData$: Subscription;
  iframeSrcParams: string;
  uvSettingsObj: any;
  readonly viewerLocation: string;
  private getManifest$: Subscription;

  constructor(private readonly imageTabSvc: ImagesTabService,
              private readonly sharedDataSvc: SharedDataService,
              private readonly route: ActivatedRoute,
              private readonly clientConfigurationSvc: ClientConfigurationService) {
    this.viewerLocation = this.clientConfigurationSvc.asset('uv-dist/index.html');
  }

  ngOnInit() {
    this.routeData$ = this.route.data.subscribe(data => this.pageTitle = data.pageTitle);
    const hubId = this.route.snapshot.paramMap.get('hubId');
    const fileUuid = this.route.snapshot.paramMap.get('fileUuid');
    const params: ManifestParams = {
      hubId: hubId,
      fileUuid: fileUuid
    };

    this.getManifest$ = this.imageTabSvc.getManifest(params)
      .finally(() => {
        if (this.uvSettingsObj.manifest) {
          this.iframeSrcParams = queryStringParser.stringify(this.uvSettingsObj);
        } else {
          console.error('Manifest URL missing !!!', this.uvSettingsObj);
        }
      })
      .subscribe((data) => {
        this.uvSettingsObj = {
          manifest: data.url,
          config: this.clientConfigurationSvc.asset('uv-dist/uv-config/uv-config.json'),
          c: 0,
          m: 0,
          s: 0,
          cv: isNaN(data.startCanvasIndex) ? 0 : data.startCanvasIndex,
          locales: 'nl-NL:Nederlands (NL),fy-NL:Frysk (FY),en-GB:English (GB)'
        };
      });
  }

  ngOnDestroy() {
    this.uvSettingsObj = {};
    if (this.getManifest$) {
      this.getManifest$.unsubscribe();
    }
    if (this.routeData$) {
      this.routeData$.unsubscribe();
    }
  }
}
