import { Component, OnDestroy } from '@angular/core';
import { TreeModel } from '../tree-model';

export interface TreeRoot {

  readonly treeModel: TreeModel;
}

@Component({
  selector: 'app-tree-root',
  templateUrl: './tree-root.component.html',
  styleUrls: ['./tree-root.component.scss']
})
export class TreeRootComponent implements TreeRoot, OnDestroy {

  readonly treeModel = new TreeModel();

  ngOnDestroy(): void {
    this.treeModel.destroy();
  }
}
