import { Component, Input, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-archive-description',
  templateUrl: './archive-description.component.html',
  styleUrls: ['./archive-description.component.scss']
})
export class ArchiveDescriptionComponent {

  @ViewChild('eadDescriptionContainer') containerElement;
  @Input() archiveDescription: any;

  panelsState = {
    panel1: false,
    panel2: false,
    panel3: false,
    panel4: false
  };

  togglePanel($event, panel: string) {
    $event.preventDefault();
    this.panelsState[panel] = !this.panelsState[panel];
  }

  expandAllPanels() {
    this.panelsState.panel1 = true;
    this.panelsState.panel2 = true;
    this.panelsState.panel3 = true;
    this.panelsState.panel4 = true;
  }
}
