import { Injectable } from '@angular/core';
import { get } from './lodash_get';
import { Subject } from 'rxjs/Subject';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { TreeNodeData } from './tree-node-data';

@Injectable({
  providedIn: 'root'
})
export class TreeService {

  private readonly $rootNodesState: Subject<void> = new ReplaySubject(1);
  private _activeNode: TreeNodeData;
  private _navigationTree: HTMLDivElement;
  private _contentTree: HTMLDivElement;

  constructor() {
  }

  /**
   * Make node active and show attention trigger
   * @param {TreeNodeData} activeNode
   */
  makeActiveNode(activeNode: TreeNodeData) {
    if (this._activeNode) {
      this._activeNode.isActive = false;
    }
    activeNode.isActive = true;
    activeNode.displayAttentionTrigger = true;
    setTimeout(() => {
      activeNode.displayAttentionTrigger = false;
    }, 3000);
    this._activeNode = activeNode;
  }

  set navigationTree(navigationTree: HTMLDivElement) {
    this._navigationTree = navigationTree;
  }

  set contentTree(contentTree: HTMLDivElement) {
    this._contentTree = contentTree;
  }

  get contentTree(): HTMLDivElement {
    return this._contentTree;
  }

  scrollNavigationTree(node: TreeNodeData) {
    this._navigationTree.scrollTop = node.navigationListElement.getBoundingClientRect().top
      - this._navigationTree.getBoundingClientRect().top
      + this._navigationTree.scrollTop
      - 100;
  }

  scrollContentTree(node: TreeNodeData) {
    this._contentTree.scrollTop = node.contentListElement.getBoundingClientRect().top
      - this._contentTree.getBoundingClientRect().top
      + this._contentTree.scrollTop
      - 50;
  }

  rootNodesStateChanged() {
    this.$rootNodesState.next();
  }

  rootNodesState(): Subject<void> {
    return this.$rootNodesState;
  }

  prepareNodes(items: Array<any>, parent?: TreeNodeData): Array<TreeNodeData> {
    items = this.prefixModelToItems(items);

    const preparedNodes = [];
    const level = !parent ? 0 : ++parent.level;
    const activeInventoryId = 'not implemented';

    items.forEach(item => {
      const preparedNode = this.prepareSingleNode(level, item, activeInventoryId);
      preparedNodes.push(preparedNode);
    });

    return preparedNodes;
  }

  private prefixModelToItems(items: Array<any>) {
    items = items.map(item => {
      return {
        model: item
      };
    });
    return items;
  }

  private prepareSingleNode(level, item, activeInventoryId: string) {
    const nodeClasses = ['branch', 'level-' + level, 'node-id-' + item.model.id];
    let hasDescendants = false;
    hasDescendants = this.hasDescenants(item, nodeClasses);

    if (item.model.id === activeInventoryId) {
      nodeClasses.push('active');
    }
    const preparedNode = this.createNode(level, item, nodeClasses, hasDescendants);
    return preparedNode;
  }

  private createNode(level: number, item, nodeClasses, hasDescendants: boolean) {
    const node = {
      ...item,
      level: level,
      isPrepared: true,
      nodeId: item.model.id,
      nodeClass: nodeClasses.join(' '),
      eadDocUrl: 'not implemented',
      unitTitle: this.getUnitTitle(item),
      unitId: item.model.unitId,
      unitAddressOrChronList: this.getAddresOrChronList(item),
      unitDate: this.getUnitDate(item),
      unitPhysicalDescription: this.getUnitPhysicalDescription(item),
      hasDescendants: hasDescendants,
      hasCustodianDigitalObject: this.isFromEDepot(item) && this.isDaoType(item) && this.hasDeliverableUnitUuid(item),
      eadViewerUrl: 'not implemented',
      viewerLinksTarget: 'not implemented',
      numberOfDigitalObjects: item.model.numberOfDigitalObjects,
      descendantsLoaded: false,
      isActive: false,
      displayAttentionTrigger: false
    };
    node.viewerParams = this.getViewerParams(node);
    return node;
  }

  private hasDescenants(item, nodeClasses) {
    if (item.model.numberOfDescendents > 0) {
      nodeClasses.push('has-descendants');
      return true;
    }
    nodeClasses.push('no-descendants');
    return false;
  }

  private findNested(obj, key, value, memo?) {
    let i;
    const proto = Object.prototype,
      ts = proto.toString,
      hasOwn = proto.hasOwnProperty.bind(obj);

    if ('[object Array]' !== ts.call(memo)) {
      memo = [];
    }
    for (i in obj) {
      if (hasOwn(i)) {
        if (key === 'head' && (i === key)) {
          if (typeof obj[i].content !== 'undefined' && value === obj[i].content.toString()) {
            memo.push(obj);
          }
        } else if (key !== 'head' && (i === key && value === obj[i])) {
          memo.push(obj);
        } else if (i === key && value === null) {
          memo.push(obj[i]);
        } else if ('[object Array]' === ts.call(obj[i]) || '[object Object]' === ts.call(obj[i])) {
          this.findNested(obj[i], key, value, memo);
        }
      }
    }

    return memo;
  }

  private getUnitTitle(item) {
    let unitTitle = '';
    if (typeof item.model.content !== 'undefined') {
      const content = this.findNested(item.model.content, 'encodinganalog', '3.1.2');
      // if the node has no title set it to '-'
      if (typeof content[0] === 'undefined') {
        unitTitle = '-';
      } else {
        content['0'].content.forEach(function (element) {
          if (typeof element === 'string') {
            unitTitle += element;
          }
          if (typeof element === 'object'
            && element
            && (typeof element.declaredType !== 'undefined')
            && element.declaredType === 'gov.loc.ead.Lb') {
            unitTitle += '<br>';
          }
        });
      }
      return unitTitle;
    }
    return item.model.unitTitle || '';
  }

  private getAddresOrChronList(item): Array<string> {
    const foundContent = [];
    if (typeof item.model.content !== 'undefined') {
      const list = this.findNested(item.model.content, 'encodinganalog', '3.6.1');

      list.forEach(function (listItem) {
        listItem.addressOrChronlistOrList.forEach(function (contentObject) {
          // Most ead do not have a type property defined.
          const type = (typeof contentObject.type === 'undefined') ? 'default' : contentObject.type;
          if (type === 'default') {
            contentObject.content.forEach(function (value) {
              foundContent.push(value);
            });
          }

          // Create an ordered list for type marked. See F1048812/invnr/C373230418 content for an example.
          if (type === 'marked') {
            let markedContent = '<ol>';
            contentObject.item.forEach(function (subItem) {
              subItem.content.forEach(function (value) {
                markedContent += '<li>' + value + '</li>';
              });
            });
            markedContent += '</ol>';
            foundContent.push(markedContent);
          }
        });
      });

      // See F1062664/invnr/C386503162 content for example. Creates a link object.
      const subContent = this.findNested(item.model.content, 'encodinganalog', '3.4.5');
      subContent.forEach(function (element) {
        let subContentValue = '';
        element.addressOrChronlistOrList[0].content.forEach(function (value) {
          if (typeof value === 'string') {
            subContentValue += value;
          }
          if (typeof value === 'object' && value.declaredType === 'gov.loc.ead.Extref') {
            subContentValue += '<a href="' + value.value.href + '">' + value.value.content[0] + '</a>';
          }
        });
        foundContent.push(subContentValue);
      });
    }

    return foundContent;
  }

  private getUnitDate(item) {
    let value = this.lodashGet(item, 'model.content.did.mdid[2].content[0]', '');
    if (typeof value === 'object') {
      value = this.lodashGet(item, 'model.content.did.mdid[2].content[0].value.content[0]', '');
    }
    return value;
  }

  private lodashGet(item, field: string, defaultValue: string) {
    return get(item, field, defaultValue);
  }

  private isObject(o) {
    return typeof o === 'object' && o !== null && !Array.isArray(o);
  }

  private hasMdidArray(item) {
    return this.isObject(item) &&
      this.isObject(item.model) &&
      this.isObject(item.model.content) &&
      this.isObject(item.model.content.did) &&
      Array.isArray(item.model.content.did.mdid);
  }

  private isFromEDepot(item) {
    if (!this.hasMdidArray(item)) {
      return false;
    }

    const mdidArray = item.model.content.did.mdid;
    for (let i = 0; i < mdidArray.length; i++) {
      const mdidObject = mdidArray[i];
      if (this.isObject(mdidObject) && Array.isArray(mdidObject.content)) {
        for (let j = 0; j < mdidObject.content.length; j++) {
          const contentItem = mdidObject.content[j];
          if (typeof contentItem === 'string' && contentItem.toLocaleLowerCase().indexOf('e-depot') > -1) {
            return true;
          }
        }
      }
    }
    return false;
  }

  private isDaoType(item): boolean {
    let value = this.lodashGet(item, 'item.model.content.mdescFull[0]', '');
    if (typeof value !== 'undefined') {
      if (typeof item.model.content.mdescFull !== 'undefined') {
        value = item.model.content.mdescFull[0].type;
      }
    }
    // DO NOT CHANGE to remove tslint warning because it will alter the behavior of the application
    return value && (value.toString() === 'dao');
  }

  private hasDeliverableUnitUuid(item) {
    const content = item.model.content;
    return content && typeof content.id === 'string' && content.id.indexOf('DU') === 0;
  }

  private getUnitPhysicalDescription(item) {
    let value = this.lodashGet(item, 'model.content.did.mdid[3].content[0]', '');
    if (typeof value === 'object') {
      value = this.lodashGet(value, 'value.content[0]', '');
    }
    return value;
  }

  private getViewerParams(item) {
    if (!item.hasCustodianDigitalObject) {
      return null;
    }
    return {
      deliverableUnitUuid: item.model.content.id.substring(2),
      cLevelId: item.model.id
    };
  }
}
