import { Component, Input, OnDestroy, OnInit, Renderer2 } from '@angular/core';
import { FacetFieldGroup, SearchFilter } from '../archives-tab.models/search-filter';
import { NavigationService } from '../../shared/services/navigation.service';
import { MultipleChoiceEntry } from '../archives-tab.models/multiple-choice-entry';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';

@Component({
  selector: 'app-search-filter',
  templateUrl: './search-filter.component.html',
  styleUrls: ['./search-filter.component.scss']
})
export class SearchFilterComponent implements OnInit, OnDestroy {

  @Input() tabLabel: string;
  @Input() tabId: string;
  @Input() searchResult: any;
  showMobileFilters = false;
  isMobile = false;
  private queryParams: any;
  private _filter: SearchFilter;

  private createQueryParams(): any {
    const filterState = {
      [NavigationService.RESET_ALL('page')]: null,
      [NavigationService.RESET_ALL('startIndex')]: null
    };

    const groups = this.filter.groupedFacetFields.concat(this.filter.groupedFacetDateFields);
    for (const group of groups) {
      const checkedOptionIds = group.entries
          .filter(entry => entry.checked)
          .map(entry => entry.id)
          .join();

      if (checkedOptionIds) {
        filterState[group.type] = checkedOptionIds;
        if (group.type === 'fromDate' || group.type === 'toDate') {
          filterState[group.type + 'Label'] = group.entries
              .filter(entry => entry.checked)
              .map(entry => `${entry.nameLabel} (${entry.frequency})`)
              .join();
        }
      } else {
        filterState[group.type] = null;
      }
    }
    return filterState;
  }

  constructor(private readonly sharedDataSvc: SharedDataService,
              private readonly navigationSvc: NavigationService,
              private breakpointObserver: BreakpointObserver,
              private renderer: Renderer2) {
  }

  @Input() set filter(filter: SearchFilter) {
    this._filter = filter;
  }

  get filter(): SearchFilter {
    return this._filter;
  }

  checkboxChanged() {
    this.queryParams = this.navigationSvc.updateQueryParams({
      queryParams: this.createQueryParams(),
      prefix: this.tabId
    });
  }

  radioButtonChanged(event, group: FacetFieldGroup, field: MultipleChoiceEntry) {
    group.entries.forEach(entry => entry.checked = entry === field);
    this.checkboxChanged();
  }

  hasFilters() {
    if (!this.filter) {
      return false;
    }
    if (this.filter.groupedFacetFields && this.filter.groupedFacetFields.length > 0) {
      return true;
    }
    return this.filter.groupedFacetDateFields && this.filter.groupedFacetDateFields.length > 0;
  }

  toggleFilterGroup(fieldGroup) {
    fieldGroup.isCollapsed = !fieldGroup.isCollapsed;
  }

  showMobileFilter(show: boolean) {
    this.showMobileFilters = show;
    if (show) {
      this.renderer.addClass(document.body, 'gaf-sidebar-open');
    } else {
      this.renderer.removeClass(document.body, 'gaf-sidebar-open');
    }
  }

  ngOnDestroy() {
    this.renderer.removeClass(document.body, 'gaf-sidebar-open');
  }

  ngOnInit() {
    this.breakpointObserver
      .observe(['(min-width: 64rem)'])
      .subscribe((state: BreakpointState) => {
        if (state.matches) {
          this.isMobile = false;
          this.renderer.removeClass(document.body, 'gaf-sidebar-open');
        } else {
          this.isMobile = true;
        }
      });
  }
}
