import { Tab } from './tab';

type Callback = (...args: any[]) => void;

export class PublicTab {

  private readonly _tab: Tab;

  constructor(tab: Tab) {
    this._tab = tab;
  }

  getId() {
    return this._tab.getId();
  }

  setTitle(title: string) {
    this._tab.setTitle(title);
  }

  setHitCount(hitCount: number) {
    this._tab.setHitCount(hitCount);
  }

  getContainer() {
    return this._tab.getContainer();
  }

  on(eventType: string, callback: Callback) {
    this._tab.on(eventType, callback);
  }
}
