import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'ead-description-block',
  templateUrl: './ead-description-block.component.html',
  styleUrls: ['./ead-description-block.component.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class EadDescriptionBlockComponent implements OnInit {
  @Input() replaceLabel = '';
  @Input() eadDocument: any;
  @Input() searchFor: string;
  @Input() searchType = 'encodinganalog';
  textContainerContent: SafeHtml;

  constructor(private sanitizer: DomSanitizer) {
  }

  ngOnInit() {
    this.textContainerContent = this.createDescription(
        this.eadDocument.content,
        this.searchFor,
        this.searchType,
        this.replaceLabel
    );
  }

  /**
   * Creates the specific description based on 'encodingAnalog' and render it as html into 'className'-container.
   * @param content
   * @param searchFor
   * @param searchType
   * @param replaceLabel
   * @returns {string | null}
   */
  createDescription(content, searchFor, searchType, replaceLabel) {
    const treeObject = this.findNested(content, searchType, searchFor);
    const unsafeHtml = this.objectToHTML(treeObject, searchType, searchFor, replaceLabel).join(' ');
    return this.sanitizer.sanitize(1, unsafeHtml);
  }

  /**
   * Function to get deeply nested objects.
   * @param content
   * @param searchType
   * @param searchFor
   * @param {any} memo
   * @returns {any}
   */
  findNested(content, searchType, searchFor, memo = null) {
    const proto = Object.prototype,
        ts = proto.toString;
    if ('[object Array]' !== ts.call(memo)) {
      memo = [];
    }
    for (const key in content) {
      if (content.hasOwnProperty(key)) {
        if (searchType === 'head' && (key === searchType)) {
          if (typeof content[key].content !== 'undefined' && searchFor === content[key].content.toString()) {
            memo.push(content);
          }
        } else if (searchType !== 'head' && (key === searchType && searchFor === content[key])) {
          memo.push(content);
        } else if (key === searchType && searchFor === null) {
          memo.push(content[key]);
        } else if ('[object Array]' === ts.call(content[key]) || '[object Object]' === ts.call(content[key])) {
          this.findNested(content[key], searchType, searchFor, memo);
        }
      }
    }
    return memo;
  }

  /**
   * Creates the specific description based on 'encodingAnalog' and render it as html into 'className'-container.
   * @param treeObject
   * @param searchType
   * @param searchFor
   * @param replaceLabel
   * @returns {any[]}
   */
  objectToHTML(treeObject, searchType, searchFor, replaceLabel) {
    const generatedHTML = [];
    treeObject.forEach(listItem => {

      if (replaceLabel !== '') {
        generatedHTML.push('<h4 class="ead-lead">' + replaceLabel + '</h4>');
      }
      // searchType is 'label'
      if (searchType === 'label') {
        if (replaceLabel === '') {
          generatedHTML.push('<h4 class="ead-lead">' + searchFor + '</h4>');
        }
        generatedHTML.push(this.parseContent(listItem));
      } else {
        if (replaceLabel === '') {

          if (this.elementExistsAndContentObjIsArray(listItem.head)) {
            listItem.head.content.forEach(value => {
              generatedHTML.push('<h4 class="ead-lead">' + value + '</h4>');
            });
          }
        }
        generatedHTML.push(this.parseContent(listItem));

        if (listItem.addressOrChronlistOrList) {
          listItem.addressOrChronlistOrList.forEach(contentObject => {
            // Most ead do not have a type property defined.
            const type = (typeof contentObject.type === 'undefined') ? 'default' : contentObject.type;
            if (type === 'default') {
              generatedHTML.push(this.parseContent(contentObject));
            }
            if ((type === 'marked' || type === 'ordered') || (contentObject.item !== 'undefined' && searchFor === '3.3.4')) {
              let listCount = 0;
              let markedContent = '<ol>';
              if (contentObject.item) {
                contentObject.item.forEach(subItem => {

                  if (this.elementExistsAndContentObjIsArray(subItem)) {
                    subItem.content.forEach(value => {
                      listCount++;
                      markedContent += '<li>' + value + '</li>';
                    });
                  }

                });
              } else {
                contentObject.content.forEach(value => {
                  listCount++;
                  markedContent += '<li>' + value + '</li>';
                });
              }
              markedContent += '</ol>';
              // check, to prevent empty '<ol>'-tags
              if (listCount > 0) {
                generatedHTML.push(markedContent);
              }
            }
          });
        }
      }
    });
    return generatedHTML;
  }

  /**
   * Parse an object and convert it to HTML
   * @param objectToParse
   * @param {string} defStartTag
   * @param {string} defEndTag
   * @returns {string}
   */
  parseContent(objectToParse, defStartTag = '<p>', defEndTag = '</p>') {
    let generatedHTML = '';
    if (typeof objectToParse.content !== 'undefined') {
      objectToParse.content.forEach(element => {
        if (typeof element === 'object') {

          switch (element.declaredType) {
            case 'gov.loc.ead.Extent':
            case 'gov.loc.ead.Language':
            case 'gov.loc.ead.Corpname':
              generatedHTML += this.parseContent(element.value, '', '');
              break;
            case 'gov.loc.ead.Extref':
              const linkText = (typeof element.value.content !== 'undefined') ? element.value.content[0] : element.value.href;
              generatedHTML += '<a href="' + element.value.href + '">' + linkText + '</a>';
              break;
            case 'gov.loc.ead.Lb':
              generatedHTML += '<br>';
              break;
            default:
              if (element && Array.isArray(element)) {
                element.forEach(subelement => {
                  generatedHTML += this.parseContent(subelement, defStartTag, defEndTag);
                });
              }

              if (this.elementExistsAndContentObjIsArray(element.value)) {
                const subTitle = element.value.content.join(' ');
                generatedHTML += `<p>${subTitle}</p>`;
              }
              break;
          }

        } else {
          if (typeof element !== 'undefined') {
            generatedHTML += element;
          }
        }
      });
      generatedHTML = defStartTag + generatedHTML + defEndTag;
    }
    return generatedHTML;
  }


  private elementExistsAndContentObjIsArray(object): boolean {
    return  typeof object === 'object' && Array.isArray( object.content);
  }
}
