import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import 'rxjs/add/operator/distinct';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
import { HttpClient, HttpParams } from '@angular/common/http';
import merge from 'deepmerge';
import { VisualSampleResponse } from '../../images-tab/images-tab.models/visual-sample-response';
import { VisualSample } from '../../images-tab/images-tab.models/visual-sample';
import { UtilService } from './util.service';
import 'rxjs-compat/add/observable/of';
import { SharedDataService } from './sharedData.service';
import { ClientConfigurationService } from './client-configuration.service';

export interface ManifestParams {
  hubId: string;
  fileUuid: string;
}

export interface VisualSamplesParams {

  rows: number;
  scrollID?: string;
  q?: string;
}

export interface Manifest {
  url: string;
  startCanvasIndex: number;
}

@Injectable()
export class ApiService {

  constructor(private readonly http: HttpClient,
              private sharedDataSvc: SharedDataService,
              private readonly clientConfigSvc: ClientConfigurationService) {
  }

  private static httpParams(params) {
    return {
      params: new HttpParams({
        fromObject: params
      })
    };
  }

  expandVisualSample(visualSample: VisualSample) {

    if (!UtilService.hasArrayItems(visualSample.fields['naa_thumbnail'])) {
      visualSample.fields['naa_thumbnail'] = this.clientConfigSvc.asset('images/noimage.png');
    } else {
      visualSample.fields['naa_thumbnail'] = visualSample.fields.naa_thumbnail[0];
    }

    if (!UtilService.hasArrayItems(visualSample.fields['dc_description'])) {
      visualSample.fields['dc_description'] = '';
    } else {
      visualSample.fields['dc_description'] = visualSample.fields.dc_description[0];
    }

    if (UtilService.hasArrayItems(visualSample.fields['naa_file-uuid'])) {
      visualSample.fields['naa_file-uuid'] = visualSample.fields['naa_file-uuid'][0];
    }

    return visualSample;
  }

  public getVisualSamples(params: VisualSamplesParams): Observable<VisualSampleResponse> {
    return this.callVisualSamplesApi(params);
  }

  getManifest(params: ManifestParams): Observable<Manifest> {
    // tslint:disable-next-line
    const url = `${environment.serverUrl}search/v2/manifest/${encodeURIComponent(params.hubId)}/${encodeURIComponent(params.fileUuid)}?itemFormat=flat`;
    return this.http.get(url)
      .map((data: Manifest) => {
        data.url = params.fileUuid ? `${data.url}?selected=${params.fileUuid}` : data.url;
        return data;
      });
  }

  private callVisualSamplesApi(params: VisualSamplesParams) {
    const url = `${environment.serverUrl}search/v1/hub`;
    const responseDefaults = {
      pager: {
        rows: 0,
        total: 0,
        scrollID: ''
      },
      items: []
    };
    return this.http.get(url, ApiService.httpParams(params))
      .map((data: VisualSampleResponse) => merge(responseDefaults, data))
      .map(data => {
        data.items.map((item) => this.expandVisualSample(item));
        const batch = {
          visualSamples: data.items,
          amountOfImagesLoaded: 0
        };
        data.items.forEach((visualSample) => visualSample.batch = batch);
        this.sharedDataSvc.updateSearchResults({hits: {archives: -1, images: data.pager.total}});

        return data;
      });
  }
}
