import { Component, ElementRef, EventEmitter, Input, Output } from '@angular/core';
import { SearchTerm } from '../search-term';
import { DescriptionHighlightsNavigator } from '../../../shared/shared.models/description-highlights-navigator';

interface SelectBoxOption {
  value: string;
  label: string;
}

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss']
})
export class SearchBarComponent {

  selectOptions: Array<SelectBoxOption>;

  @Output()
  searchTermEmitter: EventEmitter<SearchTerm>;
  @Input()
  searchContentRoot: ElementRef<HTMLDivElement>;
  @Input()
  highlightsNavigator: DescriptionHighlightsNavigator;

  currentSearchTerm: SearchTerm;

  constructor() {
    this.searchTermEmitter = new EventEmitter<SearchTerm>();
    this.selectOptions = [{
      label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-INVENTORY',
      value: 'INVENTORY'
    }, {
      label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-DESCRIPTION',
      value: 'DESCRIPTION'
    }, {
      label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-ITEM-CODE',
      value: 'ITEM_CODE'
    }];

    this.currentSearchTerm = {
      value: '',
      target: this.selectOptions[0].value
    };
  }

  onSearchBtnClick(searchTerm) {
    this.currentSearchTerm.value = searchTerm;
    this.searchTermEmitter.emit(this.currentSearchTerm);
  }

  onSearchTargetChanged(value: string) {
    this.currentSearchTerm.target = value;
  }
}
