import { NgModule } from '@angular/core';
import { RouteReuseStrategy, RouterModule } from '@angular/router';
import { CanDeactivateGuard } from './shared/guards/can-deactivate-guard.service';
import { ReuseAllComponentsStrategy } from './route-reuse-strategy';
import { APP_BASE_HREF, CommonModule, LocationStrategy, PlatformLocation } from '@angular/common';
import { QueryParamsLocationStrategy } from './query-params-location-strategy';
import { ImagesTabModule } from './images-tab/images-tab.module';
import { ArchivesModule } from './archives-tab/archives.module';
import { ArchivesTabHubModule } from './archives-tab-hub/archives-tab-hub.module';
import { RouterOutletComponent } from './shared/components/router-outlet/router-outlet.component';
import { SharedModule } from './shared/shared.module';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { PageInstituteComponent } from './page-institute/page-institute.component';

@NgModule({
  imports: [
    RouterModule.forRoot(
      [],
      {
        // enableTracing: true
      }
    ),
    ImagesTabModule,
    ArchivesModule,
    ArchivesTabHubModule,
    SharedModule,
    CommonModule
  ],
  declarations: [
    RouterOutletComponent
  ],
  entryComponents: [
    RouterOutletComponent,
    PageInstituteComponent,
    PageNotFoundComponent
  ],
  providers: [
    CanDeactivateGuard,
    // Our custom LocationStrategy does not require APP_BASE_HREF to be defined,
    // but we can not remove it because Angular will instantiate the PathLocationStrategy nonetheless
    // and this class requires the APP_BASE_HREF or <base href=".."> to be defined.
    {provide: APP_BASE_HREF, useValue: '/'},
    {
      provide: LocationStrategy,
      useClass: QueryParamsLocationStrategy,
      deps: [PlatformLocation]
    },
    {provide: RouteReuseStrategy, useClass: ReuseAllComponentsStrategy}
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {

}
