import { HighlightsNavigator } from '../highlights-navigator';
import { environment } from '../../../environments/environment';
import { TreeService } from './tree-service';
import { Pager } from './pager';
import { Subject } from 'rxjs/Subject';
import { TreeResponse } from './tree-response';
import { Subscription } from 'rxjs/Subscription';

export class TreeHighlighter implements HighlightsNavigator {

  private nextScrollID: string;
  private activeScrollID: string;
  private previousScrollIDs: string[] = [];
  private changes = new Subject<TreeResponse>();
  private subscriptions: Subscription[] = [];

  hits: number;
  cursor: number;
  hasNextHighlight: boolean;
  hasPreviousHighlight: boolean;

  constructor(private readonly request: { findingAidNo: string, url: string },
    private readonly response: TreeResponse,
    private readonly treeSvc: TreeService) {
    this.updateState(this.response);
  }

  private updateState(response: TreeResponse) {
    if (response.treeHeader && response.treeHeader.previousScrollIDs) {
      this.previousScrollIDs = response.treeHeader.previousScrollIDs;
    }

    const pager: Pager = response.pager;
    this.cursor = pager.cursor ? pager.cursor + 1 : 1;
    this.hits = pager.total;
    this.nextScrollID = pager.scrollID;

    this.hasPreviousHighlight = this.cursor > 1;
    this.hasNextHighlight = !!this.nextScrollID;
  }

  nextHighlight() {
    if (this.nextScrollID) {
      this.previousScrollIDs.push(this.nextScrollID);
      this.activeScrollID = this.nextScrollID;

      const url = this.createRequestURL(this.nextScrollID);
      this.treeSvc.getSearchPage(this.request.findingAidNo, url).subscribe((result) => {
        this.updateState(result.response);
        this.changes.next(result.response);
      });
    }
  }

  previousHighlight() {
    if (this.hasPreviousHighlight) {
      let prevScrollID = this.previousScrollIDs.pop();
      if (prevScrollID === this.activeScrollID) {
        prevScrollID = this.previousScrollIDs.pop();
      }
      this.activeScrollID = prevScrollID;

      const url = this.createRequestURL(prevScrollID);
      this.treeSvc.getSearchPage(this.request.findingAidNo, url).subscribe((result) => {
        this.updateState(result.response);
        this.changes.next(result.response);
      });
    }
  }

  createRequestURL(scrollID: string) {
    if (scrollID) {
      return `${environment.serverUrl}/search/v1/tree/${this.request.findingAidNo}?qs=${scrollID}`;
    }
    return this.request.url;
  }

  onChange(callback: (response: TreeResponse) => void) {
    this.subscriptions.push(this.changes.subscribe(callback));
  }

  removeAllSubscriptions() {
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
  }
}
