export enum Dots {
  BEFORE,
  AFTER,
  BOTH,
  NONE
}

export class Page {

  label: string;
  index: number;
  private dots = Dots.NONE;

  constructor(index: number) {
   this.setPage(index);
  }

  setPage(index: number) {
    this.label = `${index}`;
    this.index = index;
  }

  setDots(dots: Dots) {
    this.dots = dots;
  }

  hasAfterDots() {
    return this.dots === Dots.AFTER || this.dots === Dots.BOTH;
  }

  hasBeforeDots() {
    return this.dots === Dots.BEFORE || this.dots === Dots.BOTH;
  }

  getDots() {
    return this.dots;
  }
}
