import { TranslatedLabels } from '../archives-tab.models/translated-labels';
import { MultipleChoiceEntry } from '../archives-tab.models/multiple-choice-entry';
import { EADDocumentsResponse } from '../archives-tab.models/ead-documents-response';
import { FacetFieldGroup, SearchFilter, SearchFilterBase } from '../archives-tab.models/search-filter';
import { ActiveFilter } from '../archives-tab.models/active-filter';
import { SafeIdentifierPipe } from '../../shared/pipes/safe-identifier';

export class Filter implements SearchFilter {

  readonly activeFilters: ActiveFilter[];
  readonly groupedFacetDateFields: FacetFieldGroup[];
  readonly groupedFacetFields: FacetFieldGroup[];

  private static getFacetFieldGroupTitle(translatedLabels: TranslatedLabels,
                                         type: string,
                                         entries: MultipleChoiceEntry[]): string {
    const totalFrequency = entries.map(entry => entry.frequency)
      .reduce((accumulator, currentValue) => accumulator + currentValue, 0);
    const title = translatedLabels.facetFieldTitles[type];
    return `${title} (${totalFrequency})`;
  }

  private static createIdForCheckboxOrRadioButton(group: FacetFieldGroup, entry: MultipleChoiceEntry, tabId: string): string {
    return SafeIdentifierPipe.staticTransform(`${tabId}_${group.type}_${entry.id}`);
  }

  private static createNameForRadioButton(group: FacetFieldGroup, tabId: string) {
    return SafeIdentifierPipe.staticTransform(`${tabId}_${group.type}`);
  }

  private static addSafeIdsToFacetFields(groupedFacetField: FacetFieldGroup[], tabId: string) {
    groupedFacetField.forEach(group => {
      group.entries.forEach((entry) => {
        entry.safeId = Filter.createIdForCheckboxOrRadioButton(group, entry, tabId);
        entry.safeName = entry.safeId;
      });
    });
  }

  private static addSafeIdsToFacetDateFields(groupedFacetField: FacetFieldGroup[], tabId: string) {
    groupedFacetField.forEach(group => {
      group.entries.forEach((entry) => {
        entry.safeId = Filter.createIdForCheckboxOrRadioButton(group, entry, tabId);
        entry.safeName = Filter.createNameForRadioButton(group, tabId);
      });
    });
  }

  constructor(filter: SearchFilterBase, queryParams, tabId: string) {
    filter = {
      ...filter
    };

    if (!Array.isArray(filter.groupedFacetFields)) {
      filter.groupedFacetFields = this.groupFacetFieldsByType(filter as EADDocumentsResponse, filter.facetFields);
    }
    if (!Array.isArray(filter.groupedFacetDateFields)) {
      filter.groupedFacetDateFields = this.groupFacetFieldsByType(filter as EADDocumentsResponse, filter.facetDateFields);
    }

    Filter.addSafeIdsToFacetFields(filter.groupedFacetFields, tabId);
    Filter.addSafeIdsToFacetDateFields(filter.groupedFacetDateFields, tabId);

    this.updateFilterState(filter, queryParams);

    this.groupedFacetFields = filter.groupedFacetFields;
    this.groupedFacetDateFields = filter.groupedFacetDateFields;
    const allGroupedFacetFields = filter.groupedFacetFields.concat(filter.groupedFacetDateFields);
    this.activeFilters = this.createActiveFilters(allGroupedFacetFields);
  }

  createActiveFilters(groups: FacetFieldGroup[]): ActiveFilter[] {
    const activeFilters: ActiveFilter[] = [];
    for (const group of groups) {
      group.entries.filter(entry => entry.checked)
        .forEach(entry => {
          activeFilters.push({
            id: entry.id,
            groupId: group.type,
            nameLabel: entry.nameLabel,
            frequency: entry.frequency,
            disabled: entry.disabled
          });
        });
    }
    return activeFilters;
  }

  private groupFacetFieldsByType(response: SearchFilterBase, facetFields): FacetFieldGroup[] {
    if (!facetFields) {
      return [];
    }

    const groupedFacetFields: FacetFieldGroup[] = [];
    Object.keys(facetFields).forEach(key => {
      const entries: MultipleChoiceEntry[] = facetFields[key];
      groupedFacetFields.push({
        title: Filter.getFacetFieldGroupTitle(response.translatedLabels, key, entries || []),
        type: key,
        entries: entries.map(entry => {
          entry.nameLabel = response.translatedLabels.facetFieldNames[entry.name] || entry.name;
          return entry;
        })
      });
    });
    return groupedFacetFields;
  }

  private pushAdditionalDate(groupedFacetDateFields: FacetFieldGroup[], type: string, queryParams) {
    const dateId = queryParams[type];
    const dateLabel = queryParams[type + 'Label'];
    if (dateId && dateLabel) {
      const nameLabelSegments = dateLabel.split(' ');
      groupedFacetDateFields.filter(group => group.type === type)
        .forEach(group => {
          if (!group.entries.find(entry => entry.id === dateId)) {
            group.entries.push({
              id: dateId,
              name: nameLabelSegments[0],
              nameLabel: nameLabelSegments[0],
              frequency: nameLabelSegments[1].substring(1, nameLabelSegments[1].length - 1),
              checked: true
            });
          }
        });
    }
  }

  private pushAdditionalDates(groupedFacetDateFields: FacetFieldGroup[], queryParams) {
    this.pushAdditionalDate(groupedFacetDateFields, 'fromDate', queryParams);
    this.pushAdditionalDate(groupedFacetDateFields, 'toDate', queryParams);
  }

  private updateState(group: FacetFieldGroup, queryParams) {
    group.entries.forEach((entry) => {
      const propertyValue = queryParams[group.type];
      if (typeof propertyValue !== 'string' || propertyValue === '') {
        return false;
      }
      entry.checked = entry.checked === true ? true : propertyValue.indexOf(entry.id) >= 0;
      if (entry.checked) {
        group.selectedValue = entry.id;
      }
    });
  }

  private updateFilterState(filter: SearchFilterBase, queryParams) {
    filter.groupedFacetFields.forEach(groupedFacetField => {
      this.updateState(groupedFacetField, queryParams);
    });

    this.pushAdditionalDates(filter.groupedFacetDateFields, queryParams);
    filter.groupedFacetDateFields.forEach(groupedFacetField => {
      this.updateState(groupedFacetField, queryParams);
    });
  }
}
