import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { RequestInterceptor } from './shared/services/request.interceptor';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app.routing.module';
import { FormsModule } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { LayoutModule } from '@angular/cdk/layout';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { SharedModule } from './shared/shared.module';


import { SharedDataService } from './shared/services/sharedData.service';
import { ApiService } from './shared/services/api.service';
import { UtilService } from './shared/services/util.service';
import { ClientConfigurationService } from './shared/services/client-configuration.service';
import { ArchivesModule } from './archives-tab/archives.module';
import { ArchiveDetailsComponent } from './archives-tab/archive-details/archive-details.component';

import { ArchivesTabHubModule } from './archives-tab-hub/archives-tab-hub.module';

// Allows ngx-translate to json translations from /assets/i18n/xx.json (where xx is the language code)
export function createTranslateLoader(http: HttpClient, clientConfigSvc: ClientConfigurationService) {
  return new TranslateHttpLoader(http, clientConfigSvc.asset('/i18n/'), '.json');
}

@NgModule({
  declarations: [
    AppComponent,
  ],
  imports: [
    ArchivesModule,
    ArchivesTabHubModule,
    BrowserModule,
    BrowserAnimationsModule,
    LayoutModule,
    AppRoutingModule,
    FormsModule,
    SharedModule,

    // Translate
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient, ClientConfigurationService]
      }
    }),
  ],
  providers: [
    SharedDataService,
    ApiService,
    UtilService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: RequestInterceptor,
      multi: true
    }
  ],
  entryComponents: [
    ArchiveDetailsComponent,
  ],
  bootstrap: [AppComponent]

})
export class AppModule {
}
