import {Inject, Injectable} from '@angular/core';
import {WINDOW} from '../../services/window.provider';
import {parse} from 'query-string';

const FEATURE_TOGGLE_KEY = 'ft';

@Injectable({
  providedIn: 'root'
})
export class FeatureToggleService {
  private allowedHostNames = ['localhost', 'acpt.nationaalarchief.nl', 'test.nationaalarchief.nl'];
  private features: string[];

  constructor(
    @Inject(WINDOW) private window: Window,
  ) {
    this.init();
  }

  isEnabled(featureName): boolean {
    const match = featureName.match(/^(!?)(.*)$/);
    const noBang = match[1] !== '!';
    featureName = match[2];

    const containsFeature = this.features.includes(featureName) && this.allowedDomain();

    return noBang ? containsFeature : !containsFeature;
  }

  getToggles(): string[] {
    return this.allowedDomain() ? this.features : [];
  }

  private allowedDomain(): boolean {
    return this.allowedHostNames
      .filter( name => this.window.location.hostname.endsWith(name))
      .length > 0;
  }

  private init() {
    const featuresParam = parse(this.window.location.search)[FEATURE_TOGGLE_KEY] ||
      this.window.sessionStorage.getItem(FEATURE_TOGGLE_KEY);

    if (featuresParam) {
      this.window.sessionStorage.setItem(FEATURE_TOGGLE_KEY, featuresParam);
    }

    this.features = (featuresParam || '')
      .split(',')
      .filter(x => x);
  }
}
