import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { SearchResult } from './search-result';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { TreeResponse } from './tree-response';
import { TreeHighlighter } from './tree-highlighter';
import { SearchCategory } from '../search-category';

@Injectable({
  providedIn: 'root'
})
export class TreeService {

  constructor(private readonly http: HttpClient) {
  }

  searchTree(findingAidNo: string, term: string, unitID: string, target: SearchCategory): Observable<SearchResult> {
    const url = `${environment.serverUrl}/search/v1/tree/${findingAidNo}`;
    let searchType = 'byLabel';
    let idType = '';
    if (target === SearchCategory.ITEM_CODE) {
      searchType = 'byUnitID';
      idType = '&byType=file';
    }

    let queryString = `?rows=1${idType}&${searchType}=${term}`;
    if (unitID !== '') {
      queryString = queryString + `&byUnitID=${decodeURIComponent(unitID)}`;
    }
    return this.getSearchPage(findingAidNo, `${url}${queryString}`);
  }

  getSearchPage(findingAidNo: string, url: string): Observable<SearchResult> {
    return this.http.get(`${url}`).map((res: TreeResponse) => {
      const treeSvc = this;
      const request = {
        findingAidNo: findingAidNo,
        url: url
      };
      return {
        highlightsNavigator: new TreeHighlighter(request, res, treeSvc),
        response: res
      };
    });
  }
}
