import { ChangeDetectorRef, Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { Dots, Page } from '../archives-tab.models/page.model';
import { NavigationService } from '../../shared/services/navigation.service';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';

@Component({
  selector: 'app-pager',
  templateUrl: './pager.component.html',
  styleUrls: ['./pager.component.scss']
})
export class PagerComponent implements OnInit, OnDestroy {

  private queryParams$: Subscription;
  private currentPage = 1;
  private count = 10;
  private _totalPages = 0;
  navigateToPage: string;

  @Input()
  tabId: string;
  max = 0;
  pages: Page[];
  isMobile = false;

  @ViewChild('directToPage') directToPageInput: ElementRef<HTMLInputElement>;

  private static getMobilePages(totalPages: number, currentPage: number): Page[] {
    const pages: Page[] = [];
    const maxLinks = (3 > totalPages) ? totalPages : 3;
    currentPage = (currentPage > totalPages) ? 1 : currentPage;

    for (let i = 1; i <= maxLinks; i++) {
      pages.push(PagerComponent.createPage(i));
    }

    // Alter the link[3] index and beyond when we have more than maxLinks totalpages.
    // For mobile; alter the link[1] and beyond when more.
    if (totalPages > maxLinks) {
      const centerIndex = 1;
      for (let n = centerIndex; n < pages.length; n++) {
        const pageElement = pages[n];
        let page = pageElement.index;
        // Set center page number.
        if (n === centerIndex) {
          page = Math.round(totalPages / 2);
          // Center current page when not in last three and not current.
          if (page !== currentPage && currentPage <= (totalPages - centerIndex) && currentPage > centerIndex) {
            page = currentPage;
          }
          pageElement.setPage(page);
          pageElement.setDots((totalPages === maxLinks + 1) ? Dots.AFTER : Dots.BOTH);
        }
        // Get last 3 or 1 and update their page number to be the last 3 or 1.
        if (n > centerIndex) {
          page = totalPages - ((centerIndex * 2) - n);
        }
        pageElement.setPage(page);
      }
    }
    return pages;
  }

  private static getDesktopPages(totalPages: number, currentPage: number): Page[] {
    const pages: Page[] = [];
    const maxLinks = 7;

    let start = 1;
    let end = (totalPages > maxLinks) ? maxLinks : totalPages;

    // start and end are variable based on the currentPage position when there
    // are more than maxLinks total pages.
    if (totalPages > maxLinks) {
      start = (currentPage > 1) ? currentPage - 1 : currentPage;
      end = (currentPage < totalPages - 1) ? currentPage + 1 : currentPage;
    }

    // Add page 1 after page 2 when we are in a forward position.
    if (currentPage > 2 && totalPages > maxLinks) {
      pages.push(PagerComponent.createPage(1));
    }

    // Create the links from start to end.
    for (let i = start; i <= end; i++) {
      let dots = Dots.NONE;
      if (i === start && i > 2) {
        dots = Dots.BEFORE;
      }
      if (i === end && i < totalPages - 1) {
        dots = dots !== Dots.NONE ? Dots.BOTH : Dots.AFTER;
      }
      pages.push(PagerComponent.createPage(i, dots));
    }

    // Add last page if we are not on it.
    if (currentPage !== totalPages && totalPages > maxLinks) {
      pages.push(PagerComponent.createPage(totalPages));
    }
    return pages;
  }

  constructor(
    private changeDetector: ChangeDetectorRef,
    private readonly router: Router,
    private readonly navigationSvc: NavigationService,
    private readonly breakpointObserver: BreakpointObserver) {
  }

  private static createPage(index: number, dots = Dots.NONE): Page {
    const page = new Page(index);
    page.setDots(dots);
    return page;
  }

  @Input()
  set totalPages(totalPages: number) {
    this.max = totalPages;
    this._totalPages = totalPages;
    this.pages = this.getPages();
  }

  private getPages(): Page[] {
    if (this.isMobile) {
      return PagerComponent.getMobilePages(this._totalPages, this.currentPage);
    }
    return PagerComponent.getDesktopPages(this._totalPages, this.currentPage);
  }

  ngOnInit() {
    this.breakpointObserver
      .observe(['(min-width: 64rem)'])
      .subscribe((state: BreakpointState) => {
        this.isMobile = !state.matches;
        this.pages = this.getPages();
      });

    this.pages = this.getPages();

    this.queryParams$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.navigateToPage = '';
      this.currentPage = +queryParams.page || 1;
      this.count = +queryParams.resultsPerPage || 10;
      this.pages = this.getPages();
    }, this.tabId);
  }

  ngOnDestroy() {
    this.queryParams$.unsubscribe();
  }

  hasBackButton() {
    return this.currentPage > 1;
  }

  hasNextButton() {
    return this.currentPage < this._totalPages;
  }

  navigateTo(page: number) {
    this.currentPage = page;
    this.navigationSvc.updateQueryParams({
      queryParams: {
        $page: page,
        $startIndex: (page - 1) * this.count
      },
      prefix: this.tabId
    });
    window.scrollTo(0, 0);
  }

  navigateToNextPage() {
    this.navigateTo(this.currentPage + 1);
  }

  navigateToPreviousPage() {
    this.navigateTo(this.currentPage - 1);
  }

  isCurrentPage(index: number) {
    return index === this.currentPage;
  }

  navigateDirect() {
    if (this.directToPageInput.nativeElement.checkValidity()) {
      this.navigateTo(+this.navigateToPage);
      this.navigateToPage = '';
    }
  }
}
