import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Route } from '@angular/router';
import { WebcomponentApiService } from '../../shared/services/webcomponent-api.service';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { CustomTabOptions } from '../archives-tab.models/custom-tab-options';
import { NavigationService } from '../../shared/services/navigation.service';
import { SearchFilter } from '../archives-tab.models/search-filter';
import { Filter } from '../search-filter/filter';
import { Subscription } from '../../../../node_modules/rxjs/Subscription';

@Component({
  selector: 'app-custom-tab',
  templateUrl: './custom-tab.component.html',
  styleUrls: ['../archives-tab.css', './custom-tab.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CustomTabComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  private _customContentContainer: ElementRef<HTMLDivElement>;
  private customContent = '';
  totalResults: number;

  @ViewChild('customContent')
  set customContentContainer(customContentContainer: ElementRef) {
    this._customContentContainer = customContentContainer;
    if (customContentContainer) {
      this._customContentContainer.nativeElement.innerHTML = this.customContent;
    }
  }

  @Input()
  route: Route;
  customTabOptions: CustomTabOptions;
  filter: SearchFilter;

  constructor(private readonly webcomponentApiSvc: WebcomponentApiService,
              private readonly sharedDataSvc: SharedDataService,
              private readonly navigationSvc: NavigationService) {
  }

  getId() {
    return this.route.data.key;
  }

  search(request) {
    this.webcomponentApiSvc.search({
      ...request,
      // This method is called by Spyridon
      publishResponse: (customTabOptions) => {
        this.customTabOptions = customTabOptions;
        this.totalResults = customTabOptions.totalResults;
        this.filter = new Filter(customTabOptions, this.navigationSvc.getQueryParams(), this.getId());
        this.customContent = customTabOptions.html;
        if (this._customContentContainer) {
          this._customContentContainer.nativeElement.innerHTML = this.customContent;
        }
      }
    }, this.getId());
  }

  ngOnInit() {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      if (typeof queryParams.searchTerm === 'string') {
        this.search({
          queryParams: queryParams
        });
      }
    }, this.getId());

    this.sharedDataSvc.onActivate(() => {
      this.webcomponentApiSvc.activateTab(this.getId());
    }, this.getId());

    this.sharedDataSvc.onDeactivate(() => {
      this.webcomponentApiSvc.deactivateTab(this.getId());
    }, this.getId());
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
