import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { SortOption } from '../archives-tab.models/sort-option';
import { NavigationService } from '../../shared/services/navigation.service';
import { Subscription } from '../../../../node_modules/rxjs/Subscription';

@Component({
  selector: 'app-results-sort',
  templateUrl: './results-sort.component.html',
  styleUrls: ['./results-sort.component.scss']
})
export class ResultsSortComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  @Input() tabId: string;
  @Input() sortOptions: SortOption[];

  constructor(private readonly navigationSvc: NavigationService) {
  }

  ngOnInit() {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      const sortResults = queryParams.sortResults || this.sortOptions[0].value;
      this.sortOptions.forEach(option => option.selected = option.value === sortResults);
    }, this.tabId);
  }

  updateSortOptions(event) {
    const selectedValue = event.target.value;
    this.navigationSvc.updateQueryParams({
      queryParams: {$sortResults: selectedValue},
      prefix: this.tabId
    });
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
