type Callback = (...args: any[]) => void;

export class EventSource {

  private eventHandlers: Map<string, Callback[]> = new Map();

  on(eventType: string, callback: Callback) {
    let callbacks = this.eventHandlers.get(eventType);
    if (!Array.isArray(callbacks)) {
      callbacks = [];
      this.eventHandlers.set(eventType, callbacks);
    }
    callbacks.push(callback);
  }

  notify(eventType: string, args: any[] = []) {
    if (this.eventHandlers.has(eventType)) {
      const callbacks = this.eventHandlers.get(eventType);
      callbacks.forEach((callback) => {
        callback.apply(undefined, args);
      });
    }
  }
}
