import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, ParamMap} from '@angular/router';
import queryStringParser from 'query-string';
import {ClientConfigurationService} from '../../services/client-configuration.service';
import {environment} from '../../../../environments/environment';
import {ViewerService} from './viewer.service';
import {Subscription} from 'rxjs/Subscription';


@Component({
  selector: 'app-viewer',
  templateUrl: './viewer.component.html',
  styleUrls: ['./viewer.component.scss']
})
export class ViewerComponent implements OnInit, OnDestroy {

  pageTitle: string;
  iframeSrcParams: string;
  uvSettingsObj: any;
  getManifestUrl$: Subscription;
  readonly viewerLocation: string;

  constructor(private readonly activatedRoute: ActivatedRoute,
              private viewerSvc: ViewerService,
              private readonly clientConfigurationSvc: ClientConfigurationService) {
    this.viewerLocation = this.clientConfigurationSvc.asset('uv-dist/index.html');
  }

  ngOnInit() {
    this.getViewerLink(this.activatedRoute.snapshot.queryParamMap);
  }

  private getViewerLink(queryParams: ParamMap) {
    const cLevel = queryParams.get('cLevelId');
    const deliverableUnitUuid = queryParams.get('deliverableUnitUuid');
    const language = 'nl_nl';
    this.getManifestUrl$ = this.viewerSvc.getManifestUrl(cLevel, deliverableUnitUuid, language)
      .subscribe((data: any) => {
        if (data && data.length) {
          const manifestLink = `${data[0].uri}/${cLevel}/${language}`;
          this.loadViewer(manifestLink);
        }
      });
  }

  ngOnDestroy() {
    this.uvSettingsObj = {};
    this.getManifestUrl$.unsubscribe();
  }

  loadViewer(manifestUrl) {
    this.uvSettingsObj = {
      manifest: manifestUrl,
      config: this.clientConfigurationSvc.asset('uv-dist/uv-config/uv-config.json'),
      // locales: 'nl-NL:Nederlands (NL),fy-NL:Frysk (FY),en-GB:English (GB)' // Not working with
    };
    this.iframeSrcParams = queryStringParser.stringify(this.uvSettingsObj);
  }
}
