import { Component, OnDestroy } from '@angular/core';
import { environment } from '../environments/environment';
import { TranslateService } from '@ngx-translate/core';
import {ActivatedRoute, ActivationEnd, Router} from '@angular/router';
import { WebcomponentApiService } from './shared/services/webcomponent-api.service';
import { NavigationService } from './shared/services/navigation.service';
import { Subscription } from 'rxjs/Subscription';
import { RouterOutletComponent } from './shared/components/router-outlet/router-outlet.component';
import {Title} from '@angular/platform-browser';
import {FeatureToggleService} from './shared/directives/feature-toggle/feature-toggle.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy {

  private queryParamsChanged$: Subscription;
  components = ['app-images-grid'];
  showMainHeader: boolean;
  isSearchTermPresent: boolean;

  constructor(private readonly router: Router,
              private readonly activatedRoute: ActivatedRoute,
              private title: Title,
              private featureToggleSvc: FeatureToggleService,
              private readonly translateSvc: TranslateService,
              private webcomponentApiSvc: WebcomponentApiService,
              private navigationSvc: NavigationService) {

    this.router.resetConfig(webcomponentApiSvc.getAllRoutes().map(route => {
      route.component = route.component === null ? RouterOutletComponent : route.component;
      return route;
    }));
    // this language will be used as a fallback when a translation isn't found in the current language
    translateSvc.setDefaultLang(environment.defaultLanguage);
    // the lang to use, if the lang isn't available, it will use the current loader to get them
    translateSvc.use(environment.fallbackLanguage);

    this.router.events.subscribe(event => {
      if (event instanceof ActivationEnd) {
        this.showMainHeader = event.snapshot.data.showTabNavigation;

        // setting the document title including feature toggle names
        const toggles = this.featureToggleSvc.getToggles().length > 0 ? `${this.featureToggleSvc.getToggles().join()} | ` : '';
        this.translateSvc.get(this.activatedRoute.firstChild.snapshot.data.pageTitle)
          .subscribe(pageTitle => this.title.setTitle(`${toggles}${pageTitle}`));
      }
    });

    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.isSearchTermPresent = typeof queryParams.searchTerm === 'string';
    });
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
