import { Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ArchivesTabService } from '../archives-tab.service';
import { ActivatedRoute } from '@angular/router';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { EADDocumentsResponse } from '../archives-tab.models/ead-documents-response';
import { TreeService } from './tree.service';
import { TabIds } from '../../shared/components/router-outlet/tab-ids';
import { SearchTerm } from './search-term';
import { HighlightService } from '../../shared/services/highlight.service';
import { nextFrame } from '../../shared/nextFrame';
import { DescriptionHighlightsNavigator } from '../../shared/shared.models/description-highlights-navigator';
import { EadDescriptionComponent } from './ead-description/ead-description.component';
import { Subscriptions } from '../../shared/subscriptions';

export interface Search {

  readonly searchTerm: string;
  readonly highlightsNavigator: DescriptionHighlightsNavigator;
}

@Component({
  selector: 'app-archive-details',
  templateUrl: './archive-details.component.html',
  styleUrls: ['../archives-tab.css', './archive-details.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ArchiveDetailsComponent implements OnInit, OnDestroy {

  private readonly subscriptions = new Subscriptions();
  private fLevel: string;

  nodesOpen = false;
  nodes: any[] = [];
  pageTitle: string;
  eadDocuments: EADDocumentsResponse;
  selectedEadDocument: any = null;
  summaryCollapsed = true;
  shortArchiveDescriptionCutOff = 365;
  isShortArchiveDescription = false;
  showDescription = false;
  highlightsNavigator: DescriptionHighlightsNavigator;

  @ViewChild('searchContentRoot')
  readonly eadDescriptionComponent: EadDescriptionComponent;

  constructor(private readonly archivesTabSvc: ArchivesTabService,
              private readonly sharedDataService: SharedDataService,
              private readonly route: ActivatedRoute,
              private readonly treeSvc: TreeService,
              private readonly highlightSvc: HighlightService) {
    this.resetSearch();
  }

  @ViewChild('navigationTree')
  set navigationTreeElement(navigationTreeElement: ElementRef<HTMLDivElement>) {
    this.treeSvc.navigationTree = navigationTreeElement ? navigationTreeElement.nativeElement : null;
  }

  @ViewChild('contentTree')
  set contentTreeElement(contentTreeElement: ElementRef<HTMLDivElement>) {
    this.treeSvc.contentTree = contentTreeElement ? contentTreeElement.nativeElement : null;
  }

  ngOnDestroy() {
    this.subscriptions.clear();
  }

  ngOnInit() {
    // reset search and hightlight on init. 
    this.resetSearch();

    this.loadPageTitleFromRouteData();
    this.subscribeEadDocumentResponse();

    const onActivateSub = this.sharedDataService.onActivate(() => {
      this.selectedEadDocument = null;
      this.nodes = [];
      this.summaryCollapsed = true;
      this.showDescription = false;

      const repositoryCode = this.route.snapshot.paramMap.get('repositoryCode');
      const findingAidNo = this.route.snapshot.paramMap.get('findingAidNo');

      this.archivesTabSvc.getApeId(repositoryCode, findingAidNo).subscribe((response: any) => {
        this.fLevel = response.fLevel;
        this.getChildrenOf(this.fLevel);
      });
    }, TabIds.ARCHIVE_DETAILS);
    this.subscriptions.set('ON_ACTIVATE', onActivateSub);

    const onDeactivateSub = this.sharedDataService.onDeactivate(() => {
      this.subscriptions.clear();
      onDeactivateSub.unsubscribe();
    }, TabIds.ARCHIVE_DETAILS);

    this.treeSvc.rootNodesState().subscribe(() => this.checkAnyNodesOpen());
  }

  private getChildrenOf(id: string) {
    const eadContentSub = this.archivesTabSvc.getEADContent(id)
      .subscribe((eadDocument: any) => {
        this.selectedEadDocument = eadDocument;
        this.isShortArchiveDescription = this.selectedEadDocument.fullDescription.length < this.shortArchiveDescriptionCutOff;
        const eadChildrenSub = this.archivesTabSvc.getChildrenOfEADDocument(eadDocument.id).subscribe(nodes => {
          this.nodes = this.treeSvc.prepareNodes(nodes);
        });
        this.subscriptions.set('EAD_CHILDREN', eadChildrenSub);
      });
    this.subscriptions.set('EAD_CONTENT', eadContentSub);
  }

  private checkAnyNodesOpen() {
    this.nodesOpen = false;
    for (const node of this.nodes) {
      if (node.isOpen) {
        this.nodesOpen = true;
        return;
      }
    }
    return;
  }

  collapseAllNodes() {
    for (const node of this.nodes) {
      this.collapseNode(node);
    }
    this.treeSvc.rootNodesStateChanged();
  }

  private collapseNode(node) {
    node.isOpen = false;
    if (node.children) {
      for (const child of node.children) {
        this.collapseNode(child);
      }
    }
  }

  displayDescription() {
    this.showDescription = true;
    this.resetSearch();
    this.highlightSvc.removeHighlightingFrom(this.treeSvc.contentTree);
  }

  hideDescription() {
    this.showDescription = false;
    this.resetSearch();
    this.highlightSvc.removeHighlightingFrom(this.eadDescriptionComponent.eadDescriptionContainer.nativeElement);
  }

  private loadPageTitleFromRouteData() {
    // path: 'archives/:repositoryCode/:findingAidNo/:id'
    const routeDataSub = this.route.data.subscribe(data => this.pageTitle = data.pageTitle);
    this.subscriptions.set('routeData', routeDataSub);
  }

  private subscribeEadDocumentResponse() {
    this.archivesTabSvc.eadDocuments()
      .subscribe(response => {
        this.eadDocuments = response;
      });
  }

  getPeriods(mdid: any) {
    const periods = [];
    mdid.filter(obj => obj.encodinganalog === '3.1.3' && (Array.isArray(obj.content) && obj.content.length))
      .forEach(obj => periods.push(obj.content));
    return periods.join(', ');
  }

  doSearchFor(searchTerm: SearchTerm) {
    this.resetSearch();
    if (searchTerm.target === 'DESCRIPTION') {
      this.searchDescription(searchTerm.value);
    } else if (searchTerm.target === 'INVENTORY') {
      this.searchInventory(searchTerm.value);
    }
  }

  private searchInventory(searchTerm: string) {
    this.hideDescription();
    this.archivesTabSvc.searchInInventoryFor(this.fLevel, searchTerm).subscribe((searchResult) => {
      this.nodes = searchResult;
      nextFrame(() => {
        this.highlightsNavigator = this.highlightSvc.highlight(this.treeSvc.contentTree, searchTerm);
      });
    });
  }

  private searchDescription(searchTerm: string) {
    this.displayDescription();
    this.eadDescriptionComponent.expandAllPanels();
    const descriptionContainer = this.eadDescriptionComponent.eadDescriptionContainer.nativeElement;
    this.highlightsNavigator = this.highlightSvc.highlight(descriptionContainer, searchTerm);
  }

  private resetSearch() {
    this.highlightsNavigator = null;
  }
}
