import { Component, OnDestroy } from '@angular/core';
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { SharedDataService } from '../../services/sharedData.service';
import { NavigationService } from '../../services/navigation.service';
import { WebcomponentApiService } from '../../services/webcomponent-api.service';
import { Subscription } from '../../../../../node_modules/rxjs/Subscription';

export interface SearchParams {
  searchTerm: string;
}

@Component({
  selector: 'app-search-box',
  templateUrl: './search-box.component.html',
  providers: [Location, {provide: LocationStrategy, useClass: PathLocationStrategy}],
  styleUrls: ['./search-box.component.scss']
})


export class SearchBoxComponent implements OnDestroy {

  private queryParamsChanged$: Subscription;
  model: SearchParams = {searchTerm: ''};

  constructor(private sharedDataSvc: SharedDataService,
              private location: Location,
              private webcomponentApiSvc: WebcomponentApiService,
              private activatedRoute: ActivatedRoute,
              private navigationSvc: NavigationService) {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.model.searchTerm = queryParams.searchTerm;
    });
  }

  onSubmit() {
    const newQueryParams =
      this.navigationSvc.getQueryParams();
    this.navigationSvc.updateQueryParams({
      queryParams: {
        ...newQueryParams,
        [NavigationService.RESET_ALL('showDescendants')]: null,
        [NavigationService.RESET_ALL('page')]: null,
        [NavigationService.RESET_ALL('startIndex')]: null,
        searchTerm: this.model.searchTerm || ''
      }
    });

    this.webcomponentApiSvc.queryParamsChanged(this.navigationSvc.getRawQueryParams());
  }

  goBack() {
    this.location.back();
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
