const fallbackRoutes = [
  {
    path: '**',
    component: PageNotFoundComponent,
    data: {
      menuLink: 'Page not found',
      pageTitle: 'PAGE-NOT-FOUND.PAGE-TITLE',
      key: 'pageNotFound',
      showTabNavigation: false
    }
  }
];

@Injectable({
  providedIn: 'root'
})
export class WebcomponentApiService {

  private readonly webcomponentApi: WebcomponentApi;

  constructor(private readonly router: Router,
              private readonly clientConfigSvc: ClientConfigurationService) {
    this.webcomponentApi = new WebcomponentApi(routes);
    this.clientConfigSvc.submitWebcomponentApi(new PublicWebcomponentApi(this.webcomponentApi));
  }

  getAllRoutes(): Routes {
    return this.webcomponentApi.getRoutes().concat(fallbackRoutes);
  }

  getTabs(): Routes {
    return this.webcomponentApi.getTabs();
  }

  search(request, tabId: string) {
    if (tabId == null) {
      this.webcomponentApi.notifyTabs('search', [request]);
    } else {
      const tab = this.webcomponentApi.findTab(tabId);
      if (tab !== null) {
        tab.notify('search', [request]);
      }
    }
  }

  queryParamsChanged(queryParams: Params) {
    this.webcomponentApi.notify('searchTermChanged', [queryParams]);
  }

  onDetectChanges(callback: () => void) {
    this.webcomponentApi.on('detectChanges', callback);
  }

  activateTab(tabId: string) {
    this.webcomponentApi.findTab(tabId).notify('activate', []);
  }

  deactivateTab(tabId: string) {
    this.webcomponentApi.findTab(tabId).notify('deactivate', []);
  }

  archiveDetailsNodeExpanded(treeNodeData: TreeNodeData, branchElement: HTMLUListElement) {
    if (treeNodeData.hasDescendants === false) {
      this.webcomponentApi.findTab(TabIds.ARCHIVE_DETAILS)
        .notify('nodeExpanded', [treeNodeData, branchElement]);
    }
  }
}
