import { Component, ElementRef, Input, OnDestroy, ViewChild } from '@angular/core';
import { ArchivesTabService } from '../../archives-tab.service';
import { TreeService } from '../tree.service';
import { TreeNodeData } from '../tree-node-data';
import { WebcomponentApiService } from '../../../shared/services/webcomponent-api.service';
import { Subscriptions } from '../../../shared/subscriptions';

@Component({
  selector: 'app-tree-node',
  templateUrl: './tree-node.component.html',
  styleUrls: ['./tree-node.component.scss']
})
export class TreeNodeComponent implements OnDestroy {

  private readonly subscriptions = new Subscriptions();
  @Input() isRoot: boolean;
  @Input() isNavigationTree: boolean;
  @Input() node: TreeNodeData;

  @ViewChild('nodeElement')
  set nodeElement(nodeElement: ElementRef<HTMLUListElement>) {
    if (this.isNavigationTree) {
      this.node.navigationListElement = nodeElement.nativeElement;
    } else {
      this.node.contentListElement = nodeElement.nativeElement;
    }
  }

  constructor(private readonly webcomponentApiSvc: WebcomponentApiService,
              private readonly archivesTabSvc: ArchivesTabService,
              private readonly treeSvc: TreeService) {
  }

  loadChildren($event, node: TreeNodeData) {
    $event.preventDefault();
    node.isOpen = !node.isOpen;
    this.treeSvc.makeActiveNode(node);

    if (!node.isReserved) {
      node.isReserved = true;
      this.webcomponentApiSvc.archiveDetailsNodeExpanded(node, this.node.contentListElement);
    }

    if (this.isRoot) {
      this.treeSvc.rootNodesStateChanged();
    }

    if (this.isNavigationTree) {
      this.treeSvc.scrollContentTree(node);
    } else {
      this.treeSvc.scrollNavigationTree(node);
    }

    if (node.children) {
      return;
    }

    if (node.hasDescendants) {
      const parentNode = node;
      const eadChildrenSub = this.archivesTabSvc.getChildrenOfEADDocument(node.nodeId).subscribe((children) => {
        if (children) {
          children.forEach(child => child.parent = node);
          node.children = this.treeSvc.prepareNodes(children, node);
          node.isOpen = parentNode.isOpen;
        }
      });
      this.subscriptions.set('EAD_CHILDREN', eadChildrenSub);
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.clear();
  }
}
