import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../../../environments/environment';
import {Observable} from 'rxjs/Observable';

@Injectable({
  providedIn: 'root'
})

export class ViewerService {

  constructor(private http: HttpClient) {

  }

  getManifestUrl(clevel, deliverableUnitUuid, language): Observable<Object> {
    const manifestSCall = `${environment.deprecatedServerUrl}viewerlinks/${deliverableUnitUuid}/${clevel}/${language}`;
    return this.http.get(manifestSCall);
  }

}
