import { Component, OnDestroy, OnInit } from '@angular/core';
import { ArchivesTabService } from '../archives-tab/archives-tab.service';
import { Subscription } from 'rxjs/index';
import { TabIds } from '../shared/components/router-outlet/tab-ids';
import { NavigationService } from '../shared/services/navigation.service';
import {SharedDataService} from '../shared/services/sharedData.service';

@Component({
  selector: 'app-page-institute',
  templateUrl: './page-institute.component.html',
  styleUrls: ['./page-institute.component.scss']
})
export class PageInstituteComponent implements OnInit, OnDestroy {

  queryParamsChanged$: Subscription;
  instituteDataObs: any;
  readonly tabId = TabIds.INSTITUTE;

  constructor(private archivesTabSvc: ArchivesTabService,
              private readonly navigationSvc: NavigationService) {
  }

  ngOnInit() {
    this.queryParamsChanged$  = this.navigationSvc.queryParamsChanged((queryParams) => {
      if (queryParams.repositoryCode) {
        this.instituteDataObs = this.archivesTabSvc.getInstituteData(queryParams, queryParams.repositoryCode);
      } else {
        this.instituteDataObs = this.archivesTabSvc.getInstituteData(queryParams);
      }
    }, this.tabId);
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }

}
