import {Injectable} from '@angular/core';
import {nextFrame} from '../nextFrame';
import {DescriptionHighlightsNavigator} from '../shared.models/description-highlights-navigator';

const SHADOW_ROOT = 'shadowRoot';
const highlightOptions = {
  backgroundColor: '#ffff00',
  activeBackgroundColor: '#3dbae4'
};

@Injectable({
  providedIn: 'root'
})
export class HighlightService {

  /**
   * Creates a RegEx pattern that matches one or multiple whole words.
   * There appears to be a problem with Regex and the word boundary \b matching the
   * beginning of a string with a starting or ending character out of the normal 256 byte range.
   * That is why lookbehind (?<=) & lookahead (?=) assertions used instead.
   *
   * @param searchTerm String multiple search terms separated by one or more spaces.
   */
  private static createRegExp(searchTerm: string): RegExp {
    const regExPattern: string = searchTerm
      .replace(/[#-.]|[[-^]|[?|{}]/g, '\\$&') // Sanitize regex chars.
      .trim() // Trim the sting for starting or trailing spaces.
      .split(/\s+/) // Split on one or more spaces
      .map(word => `(?<=\\W|^)${word}(?=\\W|$)`) // Match whole search terms only.
      .join('\\s+'); // Join match patterns with one or more spaces.
    return new RegExp(regExPattern, 'ig');
  }

  private static highlightContent(node: Element, matcher: RegExp, highlightedNodes: Node[]) {
    if (node[SHADOW_ROOT]) {
      this.highlightContent(node[SHADOW_ROOT] as any as Element, matcher, highlightedNodes);
    }


    if (node.nodeType === Node.TEXT_NODE) {
      HighlightService.highlightText(node as any as Text, matcher, highlightedNodes);
    } else if (node.nodeType === Node.ELEMENT_NODE || node.nodeType === Node.DOCUMENT_FRAGMENT_NODE) {

      if (!(node.tagName === 'STYLE' || node.tagName === 'SCRIPT')) {
        for (let i = 0, children = node.childNodes, length = children.length; i < length; i++) {
          HighlightService.highlightContent(children[i] as any as Element, matcher, highlightedNodes);
        }
      }
    }
  }

  private static highlightText(textNode: Text, matcher: RegExp, highlightedNodes: Node[]) {
    const highlightedContent = textNode.nodeValue.replace(matcher, (match) => {
      return `<em class="highlighted" style="background-color: ${highlightOptions.backgroundColor}">${match}</em>`;
    });

    const highlightedContentContainer = document.createElement('span');
    highlightedContentContainer.setAttribute('highlight-container', 'highlight-container');
    highlightedContentContainer.innerHTML = highlightedContent;
    textNode.parentNode.replaceChild(highlightedContentContainer, textNode);
    HighlightService.collectHighlightNodes(highlightedContentContainer, highlightedNodes);
  }

  private static collectHighlightNodes(highlightedContentContainer, highlightedNodes: Node[]) {
    const highlighted = highlightedContentContainer.querySelectorAll('em.highlighted');
    for (let i = 0; i < highlighted.length; i++) {
      highlightedNodes.push(highlighted[i]);
    }
  }

  highlight(node: HTMLElement, searchTerm: string): DescriptionHighlightsNavigator {
    this.removeHighlightingFrom(node);
    const highlightedNodes: HTMLElement[] = [];
    if (searchTerm) {
      const regex = HighlightService.createRegExp(searchTerm);
      HighlightService.highlightContent(node, regex, highlightedNodes);
    }

    const navigator = new DescriptionHighlightsNavigator(node, highlightedNodes, highlightOptions);
    const headerBounds = document.getElementsByClassName('gaf-ead-header')[0].getBoundingClientRect();
    window.scrollTo(0, headerBounds.height + headerBounds.top + window.scrollY);

    nextFrame(() => {
      navigator.nextHighlight();
    });
    return navigator;
  }

  removeHighlightingFrom(node: Node) {
    if (node[SHADOW_ROOT]) {
      this.removeHighlightingFrom(node[SHADOW_ROOT]);
    }

    if (node.attributes && node.attributes.getNamedItem('highlight-container')) {
      let textContent = '';
      for (let j = 0, children = node.childNodes, length = children.length; j < length; j++) {
        textContent += children[j].textContent;
      }
      node.parentNode.replaceChild(document.createTextNode(textContent), node);
    } else if (node.nodeType === Node.ELEMENT_NODE || node.nodeType === Node.DOCUMENT_FRAGMENT_NODE) {
      for (let i = 0, children = node.childNodes, length = children.length; i < length; i++) {
        this.removeHighlightingFrom(children[i]);
      }
    }
  }
}
