import { Component, OnDestroy, OnInit } from '@angular/core';

import { Masonry } from 'ng-masonry-grid';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { VisualSample } from '../images-tab.models/visual-sample';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { ImagesTabService } from '../images-tab.service';
import { ClientConfigurationService } from '../../shared/services/client-configuration.service';
import { NavigationService } from '../../shared/services/navigation.service';
import { TabIds } from '../../shared/components/router-outlet/tab-ids';
import { RoutingService } from '../../shared/services/routing.service';

@Component({
  selector: 'app-images-grid',
  templateUrl: './images-grid.component.html',
  styleUrls: ['./images-grid.component.scss']
})

export class ImagesGridComponent implements OnInit, OnDestroy {

  readonly tabId = TabIds.IMAGE_GRID;

  random = Math.random();
  visualSamples: VisualSample[] = [];
  imagesLoadingMessage: string;
  private masonry: Masonry;
  private masonryRemoveAll$: Subscription;
  private search$: Subscription;
  private visualSamples$: Subscription;
  private queryParams: any = {};
  private isActive = false;
  totalResults: number;

  constructor(private imagesTabSvc: ImagesTabService,
              private sharedDataSvc: SharedDataService,
              private activatedRoute: ActivatedRoute,
              private clientConfigSvc: ClientConfigurationService,
              private routingService: RoutingService,
              private navigationSvc: NavigationService) {
  }

  reset() {
    this.navigationSvc.resetFilters();
  }

  ngOnInit() {
    this.sharedDataSvc.onActivate(() => {
      this.isActive = true;
    }, TabIds.IMAGE_GRID);

    this.sharedDataSvc.onDeactivate(() => {
      this.isActive = false;
    }, TabIds.IMAGE_GRID);

    this.search$ = this.navigationSvc.queryParamsChanged(queryParams => {
      this.queryParams.imagesScrollY = 0;
      this.imagesTabSvc.cancelVisualSamplesRequest();
      this.clearMasonry(queryParams.searchTerm);
    }, TabIds.IMAGE_GRID);

    this.visualSamples$ = this.imagesTabSvc.visualSamples().subscribe((visualSamples) => {
      visualSamples.forEach(sample => this.visualSamples.push(sample));
      this.totalResults = this.visualSamples.length;
    });
  }

  ngOnDestroy() {
    if (this.search$) {
      this.search$.unsubscribe();
    }
    if (this.visualSamples$) {
      this.visualSamples$.unsubscribe();
    }
    if (this.masonryRemoveAll$) {
      this.masonryRemoveAll$.unsubscribe();
    }
  }

  getVisualSamples(search) {
    this.visualSamples = [];
    if (typeof search === 'string') {
      this.imagesTabSvc.getVisualSamples(search);
    }
  }

  clearMasonry(search) {
    if (this.visualSamples.length === 0) {
      this.getVisualSamples(search);
    } else {
      this.masonryRemoveAll$ = this.masonry.removeAllItems().subscribe(() => {
        this.getVisualSamples(search);
      });
    }
  }

  onMasonryInit(masonry: Masonry) {
    this.masonry = masonry;
  }

  onScroll() {
    if (!this.isActive) {
      return;
    }
    this.imagesTabSvc.getMoreVisualSamples();
  }

  navigateTo(visualSample: VisualSample) {
    const routeParams = ['images', visualSample.meta.hubID, visualSample.fields['naa_file-uuid']];
    this.routingService.navigateTo(routeParams);
  }

  replaceNotLoadedImageUrl(visualSample: VisualSample) {
    visualSample.fields.naa_thumbnail = this.clientConfigSvc.asset('images/noimage.png');
  }

  isDescriptionMissing(description: string): boolean {
    return description === '';
  }

  imageLoaded(visualSample: VisualSample) {
    const batch = visualSample.batch;
    this.imagesLoadingMessage = batch.amountOfImagesLoaded + '/ ' + batch.visualSamples.length;
    batch.amountOfImagesLoaded++;
    if (batch.amountOfImagesLoaded === batch.visualSamples.length) {
      this.imagesLoadingMessage = '';
      batch.visualSamples.forEach(item => item.isThumbnailLoaded = true);
    }
  }

  readyVisualSamples() {
    return this.visualSamples.filter(vs => vs.isThumbnailLoaded);
  }

  loadingVisualSamples() {
    return this.visualSamples.filter(vs => !vs.isThumbnailLoaded);
  }
}

