import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { SharedDataService } from '../../services/sharedData.service';
import { SearchResultsModel } from '../../shared.models/search-results.model';
import { ActivatedRoute, NavigationEnd, Route, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { WebcomponentApiService } from '../../services/webcomponent-api.service';
import { TabIds } from '../router-outlet/tab-ids';

export interface MobileMenuItem {
  menuLink: string;
  hits?: number;
}

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit, OnDestroy {

  showMobileNavigation = false;
  tabs: Route[] = [];
  activeMobileMenu: MobileMenuItem;
  routeSubscription: Subscription;
  sharedSubscription: Subscription;

  constructor(private router: Router,
              private route: ActivatedRoute,
              private sharedDataSvc: SharedDataService,
              private webcomponentApiSvc: WebcomponentApiService,
              private changeDetector: ChangeDetectorRef) {
    // Set current active route on needed for the rendering of the template.
    this.activeMobileMenu = {
      menuLink: '',
      hits: 0
    };
  }

  ngOnInit() {
    this.sharedSubscription = this.sharedDataSvc.searchResultsChanged().subscribe((searchResults: SearchResultsModel) => {
      if (searchResults) {
        this.tabs = this.webcomponentApiSvc.getTabs();
        // Enrich routes with search results hits
        this.enrich(searchResults.hits.images, TabIds.IMAGE_GRID);
        this.enrich(searchResults.hits.archives, TabIds.ARCHIVES);
        // Add functionality to use hub3 peek API
        this.enrich(0, TabIds.ARCHIVES_HUB);

        this.setCurrentRoute(this.router);
      }
    });

    // Set current route on route change
    this.routeSubscription = this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.setCurrentRoute(event);

      }
    });

    this.webcomponentApiSvc.onDetectChanges(() => {
      this.changeDetector.detectChanges();
    });
    this.changeDetector.detectChanges();
  }

  ngOnDestroy() {
    if (this.sharedSubscription instanceof Subscription) {
      this.sharedSubscription.unsubscribe();
    }
    if (this.routeSubscription instanceof Subscription) {
      this.routeSubscription.unsubscribe();
    }
  }

  toggleMobileNavigation() {
    this.showMobileNavigation = !this.showMobileNavigation;
  }

  private setCurrentRoute(router) {
    const segments = router.url.match(/[\w\-]+/);
    if (segments) {
      const currentPath = segments[0];
      const currentTab = this.tabs.filter(route => route.path === currentPath)[0];
      if (currentTab && currentTab.data && currentTab.data.menuLink) {
        this.activeMobileMenu = {
          menuLink: currentTab.data.menuLink,
          hits: currentTab.data.hits
        };
      }
    }
  }

  private enrich(hits: number, key: string) {
    const matches = this.tabs.filter(tab => tab && tab.data && tab.data.key === key);
    if (hits !== -1) {
      matches.map(tab => tab.data.hits = hits || 0);
    }
  }
}
