import { Route } from '@angular/router';
import { EventSource } from './event-source';
import { WebcomponentApi } from './webcomponent-api';

export class Tab extends EventSource {

  private readonly webcomponentApi: WebcomponentApi;
  private readonly route;
  private isTabActive = false;

  constructor(webcomponentApi, route: Route) {
    super();
    this.webcomponentApi = webcomponentApi;
    this.route = route;
  }

  setTitle(title: string) {
    this.route.data.menuLink = title;
    this.webcomponentApi.detectChanges();
  }

  setHitCount(hitCount: number) {
    this.route.data.hits = hitCount;
    this.webcomponentApi.detectChanges();
  }

  getId(): string {
    return this.route.data.key;
  }

  setActive(isActive: boolean) {
    this.isTabActive = isActive;
  }

  isActive() {
    return this.isTabActive;
  }

  getContainer() {
    if (!this.route.data || !this.route.data.containerElement) {
      throw new Error('Container of this tab is not accessible');
    }
    return this.route.data.containerElement;
  }

  getRoute(): Route {
    return this.route;
  }
}
