import {Injectable} from '@angular/core';
import {parse, stringify} from 'query-string';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {Subscription} from 'rxjs/Subscription';
import {PlatformLocation} from '@angular/common';
import {QueryParamsUpdate} from '../shared.models/query-params-update';
import {Subject} from 'rxjs/Subject';

@Injectable({
  providedIn: 'root'
})
export class NavigationService {

  private readonly noPrefix = '$none';
  private currentQueryParams: Params;
  private readonly queryParamsHistory = {};
  private $queryParamsPushed: Subject<Params> = new Subject();

  static RESET_ALL(s: string): string {
    return '_$' + s;
  }

  constructor(private readonly router: Router,
              private readonly activatedRoute: ActivatedRoute,
              private readonly location: PlatformLocation) {
    this.setCurrentQueryParams(parse(this.location.search));

    this.activatedRoute.queryParams.subscribe((queryParams) => {
      if (areObjectsEqual(this.currentQueryParams, queryParams)) {
        return;
      }
      this.$queryParamsPushed.next(queryParams);
      this.setCurrentQueryParams(queryParams);
    });

    const instance = this;
    this.location.onPopState(function () {
      instance.historyChanged();
    });
  }

  // TODO use this method instead of activated route in router-outlet component
  queryParamsChanged(onChange: (newQueryParams: Params) => void, prefix?: string): Subscription {
    this.registerPrefix(prefix || this.noPrefix);
    this.setCurrentQueryParams(this.currentQueryParams);
    const currentQueryParamsDecoded = decodeQueryParams(prefix, this.currentQueryParams);
    onChange(currentQueryParamsDecoded);

    return this.$queryParamsPushed.subscribe((queryParams) => {
      const newQueryParams = decodeQueryParams(prefix, queryParams);
      const currentQueryParams = decodeQueryParams(prefix, this.queryParamsHistory[prefix || this.noPrefix]);
      if (!areObjectsEqual(currentQueryParams, newQueryParams)) {
        onChange({...newQueryParams});
      }
    });
  }

  updateQueryParams(update: QueryParamsUpdate): void {
    const prefix = update.prefix;
    this.registerPrefix(prefix || this.noPrefix);
    const encodedQueryParams = encodeQueryParams(prefix, update.queryParams);
    const newQueryParams = this.mergeWithCurrentQueryParams(encodedQueryParams);

    if (update.notifySubscribers !== false) {
      this.$queryParamsPushed.next(newQueryParams);
    }
    this.setCurrentQueryParams(newQueryParams);

    if (update.history === 'replace') {
      const newQueryString = stringifyQueryParams(newQueryParams);
      this.location.replaceState(history.state, document.title, newQueryString);
    } else {
      this.router.navigate([], {
        queryParams: newQueryParams,
        queryParamsHandling: 'merge'
      });
    }
  }

  getQueryParams(tabId?: string): Params {
    return {...decodeQueryParams(tabId, this.currentQueryParams)};
  }

  getRawQueryParams() {
    const instance = this;
    const path = this.router.url.split('?')[0];
    return {
      ...instance.currentQueryParams,
      path: path
    };
  }

  resetFilters() {
    const nonFilteringPropertyNames = ['searchTerm', 'sortResults', 'resultsPerPage'];
    const resettedParams = Object.keys(this.currentQueryParams)
      .reduce((result, key) => {
        if (!nonFilteringPropertyNames.find(name => queryParamKeyMatch(key, name))) {
          delete result[key];
        }
        return result;
      }, {...this.currentQueryParams});

    this.router.navigate([''], {queryParams: resettedParams});
  }

  private updatePrefixedPropertyInstances(target: Params): Params {
    const copyOfTarget = {...target};

    Object.keys(copyOfTarget)
      .filter(key => isResetAllKey(key))
      .forEach(key => {
        const propertyName = key.substring(2);
        const newValue = copyOfTarget[key];
        this.updateMatchingProperties(copyOfTarget, propertyName, newValue);
        delete copyOfTarget[key];
      });
    return copyOfTarget;
  }

  private updateMatchingProperties(copyOfTarget, propertyName, newValue) {
    const matcher = new RegExp('^_.+_' + propertyName + '$');
    Object.keys(copyOfTarget).forEach(key => {
      if (matcher.test(key)) {
        copyOfTarget[key] = newValue;
      }
    });
  }

  private convertPrimitivesToString(queryParams: Params) {
    Object.keys(queryParams).forEach(key => {
      const value = queryParams[key];
      if (typeof value === 'number' || typeof value === 'boolean') {
        queryParams[key] = value + '';
      }
    });
  }

  private mergeWithCurrentQueryParams(queryParams: Params): Params {
    const newQueryParams = {...this.currentQueryParams, ...queryParams};
    const prefixedPropertiesUpdated = this.updatePrefixedPropertyInstances(newQueryParams);
    this.convertPrimitivesToString(prefixedPropertiesUpdated);
    return prefixedPropertiesUpdated;
  }

  private setCurrentQueryParams(newQueryParams: Params) {
    this.currentQueryParams = newQueryParams;
    Object.keys(this.queryParamsHistory).forEach(key => this.queryParamsHistory[key] = newQueryParams);
  }

  private registerPrefix(prefix: string) {
    if (!(prefix in this.queryParamsHistory)) {
      this.queryParamsHistory[prefix] = null;
    }
  }

  private historyChanged() {
    const queryParams: Params = parse(this.location.search);
    this.$queryParamsPushed.next(queryParams);
    this.currentQueryParams = queryParams;
  }
}


function queryParamKeyMatch(key: string, name: string): boolean {
  const keyMatch = new RegExp(`_.*_${name}`);
  return (key === name || keyMatch.test(key));
}

function stringifyQueryParams(queryParams: Params): string {
  return '?' + (stringify(queryParams) || '');
}

function encodeQueryParams(tabId: string, queryParams: Params): Params {
  const encodedQueryParams = {};
  Object.keys(queryParams).forEach(key => {
    const value = queryParams[key];
    if (tabId && key.charAt(0) === '$') {
      encodedQueryParams['_' + tabId + '_' + key.substring(1)] = value;
    } else {
      encodedQueryParams[key] = value;
    }
  });
  return encodedQueryParams;
}

function decodeQueryParams(tabId: string, queryParams: Params): Params {
  if (!queryParams) {
    return null;
  }

  const decodedQueryParams = {};
  Object.keys(queryParams).forEach(key => {
    const value = queryParams[key];
    if (key.charAt(0) === '_') {
      if (tabId && key.indexOf(tabId + '_') === 1) {
        decodedQueryParams[key.substring(tabId.length + 2, key.length)] = value;
      }
    } else {
      decodedQueryParams[key] = value;
    }
  });
  return decodedQueryParams;
}

function areObjectsEqual(currentQueryParams, newQueryParams) {
  if (!currentQueryParams) {
    return false;
  }
  const keys = Object.keys(currentQueryParams).concat(Object.keys(newQueryParams));
  for (const key of keys) {
    if (currentQueryParams[key] !== newQueryParams[key]) {
      return false;
    }
  }
  return true;
}

function isResetAllKey(key: string) {
  return key.length > 2 && key.charAt(0) === '_' && key.charAt(1) === '$';
}
