import { Injectable } from '@angular/core';
import { VisualSampleResponse } from './images-tab.models/visual-sample-response';
import { ApiService, Manifest, ManifestParams } from '../shared/services/api.service';
import { VisualSample } from './images-tab.models/visual-sample';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/distinct';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/map';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ImagesTabService {

  private visualSamplesSubject: Subject<VisualSample[]> = new Subject();
  private rows = 25;
  private scrollID: string;
  private activeRequest: Subscription;

  constructor(private apiSvc: ApiService) {
  }

  visualSamples(): Subject<VisualSample[]> {
    return this.visualSamplesSubject;
  }

  private fetchVisualSamples(queryParams) {
    this.activeRequest = this.apiSvc.getVisualSamples(queryParams)
      .finally(() => {
        this.activeRequest = null;
      })
      .subscribe((data: VisualSampleResponse) => {
          this.scrollID = data.pager.scrollID;
          this.visualSamplesSubject.next(data.items);
        },
        () => {
          this.visualSamplesSubject.next([]);
        },
      );
  }

  getVisualSamples(searchTerm: string): void {
    this.scrollID = null;
    this.cancelVisualSamplesRequest();
    this.fetchVisualSamples({q: searchTerm, rows: this.rows});
  }

  getMoreVisualSamples() {
    if (this.activeRequest) {
      return;
    }
    if (!this.scrollID) {
      this.visualSamplesSubject.next([]);
      return;
    }
    this.fetchVisualSamples({scrollID: this.scrollID});
  }

  getManifest(params: ManifestParams): Observable<Manifest> {
    return this.apiSvc.getManifest(params);
  }

  cancelVisualSamplesRequest() {
    if (this.activeRequest) {
      this.activeRequest.unsubscribe();
      this.activeRequest = null;
    }
  }
}
