import { Component, Input, OnInit } from '@angular/core';
import { TreeNode } from '../tree-node';
import { TreeNodeService } from '../tree-node.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-tree-node',
  templateUrl: './tree-node.component.html',
  styleUrls: ['./tree-node.component.scss']
})
export class TreeNodeComponent implements OnInit {

  model: TreeNode;

  @Input()
  set node(node: TreeNode) {
    this.model = node;
  }

  constructor(private readonly activatedRoute: ActivatedRoute,
              private readonly treeNodeSvc: TreeNodeService) {
  }

  ngOnInit() {
  }

  click() {
    this.model.data.treeModel.clicked(this.model);
    if (!this.model.hasChildren) {
      return;
    }
    this.collapseOrExpand();
    this.loadChildren();
  }

  private collapseOrExpand() {
    this.model.data.isExpanded = !this.model.data.isExpanded;
    if (!this.model.data.isExpanded) {
      this.collapseDescendants(this.model);
    }
  }

  private loadChildren() {
    if (this.model.inline) {
      return;
    }
    const findingAidNo = this.activatedRoute.snapshot.paramMap.get('findingAidNo');
    this.treeNodeSvc.getChildren(findingAidNo, this.model.cLevel).subscribe(children => {
      this.model.inline = children.filter(child =>
        !this.model.data.treeModel.isNavigationTree || child.hasChildren);
      this.model.data.treeModel.initializeNodes(this.model, children);
    });
  }

  private collapseDescendants(node: TreeNode) {
    if (node.inline) {
      node.inline.forEach(child => {
        if (child.data.isExpanded) {
          child.data.isExpanded = false;
          this.collapseDescendants(child);
        }
      });
    }
  }
}
