import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { TreeNode } from './tree-node';
import { ChildrenResponse } from './children-response';

@Injectable({
  providedIn: 'root'
})
export class TreeNodeService {

  constructor(private readonly http: HttpClient) {
  }

  getChildren(findingAidNo: string, nodeId: string): Observable<TreeNode[]> {
    const url = `${environment.serverUrl}/search/v1/tree/${findingAidNo}/${encodeURIComponent(nodeId)}`;
    return this.http.get(url)
      .map((res: ChildrenResponse) => {
        return res.tree;
      });
  }
}
