import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ResultOption } from '../archives-tab.models/ead-documents-response';
import { NavigationService } from '../../shared/services/navigation.service';
import { Subscription } from '../../../../node_modules/rxjs/Subscription';

@Component({
  selector: 'app-results-per-page',
  templateUrl: './results-per-page.component.html',
  styleUrls: ['./results-per-page.component.scss']
})
export class ResultsPerPageComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  @Input()
  tabId: string;

  searchOptions = [
    {value: '10', selected: true},
    {value: '20', selected: false},
    {value: '50', selected: false}
  ];

  private selectedSearchOption: ResultOption = this.searchOptions[0];

  constructor(private readonly navigationSvc: NavigationService) {
  }

  ngOnInit() {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      const resultsPerPage = queryParams.resultsPerPage || '10';
      this.searchOptions.forEach(option => option.selected = option.value === resultsPerPage);
    }, this.tabId);
  }

  onUpdateRequestOptions($event) {
    const selectedOption = $event.target.value;

    this.searchOptions.map(option => {
      option.value === selectedOption ?
        option.selected = true :
        option.selected = false;
    });

    this.selectedSearchOption = this.searchOptions
      .filter(option => option.selected)[0];

    const queryParams = this.navigationSvc.getQueryParams(this.tabId);
    const currentStartIndex = queryParams.startIndex || 0;

    let newPage = 1;
    let newStartIndex = 0;
    const count = +this.selectedSearchOption.value;
    if (currentStartIndex >= +this.selectedSearchOption.value) {
      newStartIndex = currentStartIndex - (currentStartIndex % count);
      newPage = newStartIndex / count + 1;
    }

    this.navigationSvc.updateQueryParams({
      queryParams: {
        $resultsPerPage: this.selectedSearchOption.value,
        $page: newPage,
        $startIndex: currentStartIndex
      },
      prefix: this.tabId
    });
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
