import { PublicTab } from './public-tab';
import { WebcomponentApi } from './webcomponent-api';
import { TabEntry } from './tab-entry';

type Callback = (...args: any[]) => void;

export class PublicWebcomponentApi {

  private readonly _webcomponentApi: WebcomponentApi;

  constructor(webcomponentApi: WebcomponentApi) {
    this._webcomponentApi = webcomponentApi;
  }

  addTab(options): PublicTab {
    return this._webcomponentApi.addTab(options);
  }

  display(tabEntries: TabEntry[]) {
    this._webcomponentApi.display(tabEntries);
  }

  on(eventType: string, callback: Callback, tabId: string) {
    if (tabId) {
      this._webcomponentApi.findTab(tabId).on(eventType, callback);
    } else {
      this._webcomponentApi.on(eventType, callback);
    }
  }
}

