import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'safeIdentifier',
  pure: true
})
export class SafeIdentifierPipe implements PipeTransform {

  static staticTransform(value: any, args?: any): any {
    if (value) {
      return value
        .trim()
        .replace(/^\d+/, '') // Strip numbers at the beginning of the string. Ids may not start with number
        .replace(/[.,\/#!$@%\^&\*;:{}=_`~().+]/g, '') // Remove all .,/#!$@%^&*;:{}=_`~()+ ids infriendly chars
        .replace(/(\s+)/g, '-') // Replace one or multiple spaces with dash
        .toLowerCase(); // Make the whole string lower case
    } else {
      return '';
    }
  }

  transform(value: any, args?: any): any {
    return SafeIdentifierPipe.staticTransform(value, args);
  }
}
