import { LocationChangeListener, LocationStrategy, PlatformLocation } from '@angular/common';
import { Injectable } from '@angular/core';
import { parse } from 'query-string';
import merge from 'deepmerge';

@Injectable()
export class QueryParamsLocationStrategy implements LocationStrategy {

  constructor(
    private platformLocation: PlatformLocation) {
  }

  back(): void {
    this.platformLocation.back();
  }

  forward(): void {
    this.platformLocation.forward();
  }

  getBaseHref(): string {
    return '';
  }

  onPopState(fn: LocationChangeListener): void {
    this.platformLocation.onPopState(fn);
    this.platformLocation.onHashChange(fn);
  }

  path(includeHash?: boolean): string {
    const queryParams = parse(this.platformLocation.search);
    const path = (queryParams.path || queryParams.activeTab || '') + this.platformLocation.search;
    return path;
  }

  prepareExternalUrl(internal: string): string {
    const externalUrl = window.location.pathname + this.makeNewUrl(internal);
    return externalUrl;
  }

  private mergeParams(queryString: string, additionalParams: any): string {
    const queryParams = parse(queryString);
    const newQueryParams = merge(queryParams, additionalParams);
    let newQueryString = '';
    Object.keys(newQueryParams).forEach(key => {
      if (newQueryString) {
        newQueryString += '&';
      }
      newQueryString += `${key}=${newQueryParams[key]}`;
    });
    return newQueryString;
  }

  private makeNewUrl(url: string) {
    const internalUrlFragments = url.split('?');
    let externalUrl;
    if (internalUrlFragments.length === 1) {
      externalUrl = this.mergeParams('', {
        path: internalUrlFragments[0]
      });
    } else {
      externalUrl = this.mergeParams(internalUrlFragments[1], {
        path: internalUrlFragments[0]
      });
    }
    return '?' + externalUrl;
  }

  pushState(state: any, title: string, url: string, queryParams: string): void {
    const newUrl = this.makeNewUrl(url);
    this.platformLocation.pushState(state, title, newUrl);
  }

  replaceState(state: any, title: string, url: string, queryParams: string): void {
    const newUrl = this.makeNewUrl(url);
    this.platformLocation.replaceState(state, title, newUrl);
  }
}
