import { Injectable } from '@angular/core';
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/do';
import { SharedDataService } from './sharedData.service';
import { finalize } from 'rxjs/operators';

/**
 * Makes sure the spinner is displayed while requests are pending
 */
@Injectable()
export class RequestInterceptor implements HttpInterceptor {

  private readonly minScreenTime = 500;
  private pendingRequests = 0;
  private timeSpinnerWasMadeVisible = 0;

  constructor(private sharedDataSvc: SharedDataService) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (this.pendingRequests === 0) {
      this.sharedDataSvc.showMainSpinner();
      this.timeSpinnerWasMadeVisible = new Date().getTime();
    }
    this.pendingRequests++;

    return next.handle(request).pipe(finalize(() => {
      this.completeRequest();
    }));
  }

  private completeRequest() {
    this.pendingRequests--;
    if (this.pendingRequests === 0) {
      const amountOfTimeSpinnerWasDisplayed = new Date().getTime() - this.timeSpinnerWasMadeVisible;
      const timeout = Math.max(this.minScreenTime - amountOfTimeSpinnerWasDisplayed, 0);
      setTimeout(() => {
        if (this.pendingRequests === 0) {
          this.sharedDataSvc.hideMainSpinner();
        }
      }, timeout);
    }
  }
}
