import { Component, OnInit, OnDestroy } from '@angular/core';
import { SharedDataService } from '../../services/sharedData.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-main-spinner',
  templateUrl: './main-spinner.component.html',
  styleUrls: ['./main-spinner.component.scss']
})
export class MainSpinnerComponent implements OnInit, OnDestroy {

  private subscription: Subscription;
  isVisible = false;

  constructor(private componentService: SharedDataService) {
  }

  ngOnInit() {
    this.subscription = this.componentService.mainSpinnerChanged(changes => {
      this.isVisible = changes.isVisible;
    });
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
