import { Injectable } from '@angular/core';
import { EADDocumentsResponse } from './archives-tab.models/ead-documents-response';
import { Subject } from 'rxjs/Subject';
import { ApiService } from '../shared/services/api.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { EADDocument } from './archives-tab.models/ead-document';
import { Observable } from '../../../node_modules/rxjs/Observable';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { TabIds } from '../shared/components/router-outlet/tab-ids';
import { NavigationService } from '../shared/services/navigation.service';
import { SharedDataService } from '../shared/services/sharedData.service';
import 'rxjs/add/operator/concatMap';
import 'rxjs/add/operator/mergeAll';

import { combineAll, last, map, tap } from 'rxjs/operators';
import { concat, of, forkJoin } from 'rxjs';
import 'rxjs/add/operator/concatAll';
import 'rxjs/add/operator/combineAll';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/observable/forkJoin';
import { TreeService } from './archive-details/tree.service';
import { TreeNodeData } from './archive-details/tree-node-data';
import { isNonEmptyString } from '../shared/util';

@Injectable({
  providedIn: 'root'
})
export class ArchivesTabService {

  private readonly eadDocuments$ = new Subject<EADDocumentsResponse>();
  private $eadDocumentsResponse: Subscription;

  constructor(private readonly activatedRoute: ActivatedRoute,
              private readonly navigationSvc: NavigationService,
              private readonly apiSvc: ApiService,
              private readonly http: HttpClient,
              private readonly sharedDataSvc: SharedDataService,
              private readonly treeSvc: TreeService) {
  }

  eadDocuments(): Subject<EADDocumentsResponse> {
    return this.eadDocuments$;
  }

  private callEADDocumentsApi(payload) {
    const url = `${environment.deprecatedServerUrl}search/json`;
    return this.http.post(url, payload)
      .map((response: EADDocumentsResponse) => {
        response.resultOptions = [{
          value: payload['resultsPerPage'],
          selected: true
        }];

        this.sharedDataSvc.updateSearchResults({hits: {archives: response.totalResults, images: -1}});
        return response;
      });
  }

  expandDescendants(documents: EADDocument[]) {
    const descendantDocumentId = this.navigationSvc.getQueryParams(TabIds.ARCHIVES).showDescendants;
    if (!descendantDocumentId) {
      return;
    }

    documents.filter(document => document.id === descendantDocumentId)
      .forEach(document => this.loadDescendants(document));
  }

  getEADDocuments(payload: Params) {
    this.computerPager(payload);
    if (this.$eadDocumentsResponse) {
      this.$eadDocumentsResponse.unsubscribe();
    }
    this.$eadDocumentsResponse = this.callEADDocumentsApi(payload)
      .finally(() => {
        this.$eadDocumentsResponse = null;
      })
      .subscribe((response) => {
        this.expandDescendants(response.eadDocList);
        this.eadDocuments$.next(response);
      });
  }

  private computerPager(queryParams: any) {
    queryParams.resultsPerPage = queryParams.resultsPerPage || 10;
    queryParams.language = queryParams.language || 'NL';
    queryParams.page = +queryParams.page > 0 ? (queryParams.page - 1) : 0;
    queryParams.startIndex = queryParams.page * +queryParams.resultsPerPage;
  }

  loadDescendants(document: EADDocument, page?: number) {
    if (!document.descendantsVisible) {
      document.descendantsLoading = true;

      const queryParams = this.navigationSvc.getQueryParams(TabIds.ARCHIVES);
      const descendantsPage = page || queryParams.descendantsPage || 1;
      const params = {
        ...queryParams,
        descendantsPage: descendantsPage
      };

      this.getEADDocumentDescendantsOf(params, document.id).subscribe((components: any) => {
        document.components = components.eadSearchResults;
        document.descendantsVisible = true;
        document.descendantsLoading = false;
        document.descendantsTotal = components.totalResults;
        document.totalComponentPages = components.totalPages;

        this.navigationSvc.updateQueryParams({
          queryParams: {
            $descendantsPage: descendantsPage,
            $showDescendants: document.id
          },
          prefix: TabIds.ARCHIVES,
          history: 'replace',
          notifySubscribers: false
        });
      });
    } else {
      this.navigationSvc.updateQueryParams({
        queryParams: {
          $descendantsPage: null,
          $showDescendants: null
        },
        prefix: TabIds.ARCHIVES,
        history: 'replace',
        notifySubscribers: false
      });

      document.descendantsLoading = false;
      document.descendantsVisible = false;
      document.components = null;
    }
  }

  getEADDocumentDescendantsOf(params, documentId: string): Observable<any> {
    const url = `${environment.deprecatedServerUrl}search/descendants/${documentId}`;
    const descendantsPage = params.descendantsPage ? (+params.descendantsPage - 1) : 0;
    const resultsPerPage = 10;

    return this.http.post(url, {
      descendantsPage: descendantsPage,
      showDescendants: documentId,
      language: params.language || 'NL',
      query: params.searchTerm || '',
      resultsPerPage: resultsPerPage,
      startIndex: descendantsPage * resultsPerPage
    }).map((response: any) => {
      if (!(response.eadSearchResults instanceof Array)) {
        return [];
      }
      return response;
    }) as Observable<any>;
  }

  getInstituteData(queryParams, instituteRepositoryCode: string = environment.defaultInstituteRepositoryCode) {
    const url = `${environment.deprecatedServerUrl}retrieve/archives/${instituteRepositoryCode}`;
    this.computerPager(queryParams);
    return this.http.post(url, {
        'page': queryParams.page,
        'resultsPerPage': queryParams.resultsPerPage,
        'startIndex': queryParams.startIndex, // backend will override this value
        'docType': 'fa',
        'sortRequest': {
          'sortType': 'asc',
          'fields': [
            'findingAidNo'
          ]
        }
      }
    );
  }

  getApeId(repositoryCode: string, findingAidNo: string) {
    return this.http.post(`${environment.deprecatedServerUrl}urlrewrite/getapeid/`, {
      repositoryCode: repositoryCode,
      toegangNr: findingAidNo,
      bestanddeel: ''
    });
  }

  private getDescription(eadDocument: any) {
    if (!eadDocument.content || !eadDocument.content.archdesc) {
      return '';
    }
    const fullDescription = eadDocument.content.archdesc.mdescFull;
    if (!Array.isArray(fullDescription) || !fullDescription.length) {
      return '';
    }
    const addressList = fullDescription[0].addressOrChronlistOrList;
    if (!Array.isArray(addressList) || !addressList.length) {
      return '';
    }
    const content = addressList[0].content;
    if (!Array.isArray(content) || !content.length) {
      return '';
    }
    return content[0];
  }

  getEADContent(documentId: string): Observable<EADDocument> {
    return this.http.post(`${environment.deprecatedServerUrl}search/ead/${documentId}`, {})
      .map((eadDocument: any) => {
        eadDocument.fullDescription = this.getDescription(eadDocument);
        return eadDocument;
      }) as Observable<EADDocument>;
  }

  private searchAllDescendants(documentId: string, searchTerm: string, page: number) {
    return this.searchDescendants(documentId, searchTerm, page).flatMap((response: any) => {
      return forkJoin(this.additionalPages(page + 1, response.totalPages)
        .map(nextPage => this.searchDescendants(documentId, searchTerm, nextPage)));
    });
  }

  private searchDescendants(documentId: string, searchTerm: string, page: number) {
    return this.http.post(`${environment.deprecatedServerUrl}search/descendantsWithAncestors/${documentId}`, {
      resultsPerPage: 50,
      page: page,
      query: searchTerm
    }).map((response: any) => response.eadSearchResults);
  }

  searchInInventoryFor(documentId: string, searchTerm: string): Observable<any[]> {
    const rootNode: any = {nodeId: documentId};
    return this.searchAllDescendants(documentId, searchTerm, 0)
      .flatMap((matches: any[]) => {
        const treeRoot = this.makeTree(matches)[documentId] || [];
        return this.resolveTree(rootNode, treeRoot, true);
      })
      .pipe(last())
      .map(() => rootNode.children);
  }

  private resolveTree(parentNode: TreeNodeData, branch, isRoot: boolean) {
    return this.getChildrenOfEADDocument(parentNode.nodeId).flatMap((children: any[]) => {
      parentNode.children = this.treeSvc.prepareNodes(children, isRoot ? null : parentNode);
      const nodeIdsInBranch = Object.keys(branch);
      return parentNode.children
        .filter(child => nodeIdsInBranch.indexOf(child.nodeId) >= 0)
        .map(child => {
          child.isOpen = true;
          return this.resolveTree(child, branch[child.nodeId], false);
        });
    });
  }

  private makeTree(searchResults) {
    const tree = {};

    searchResults.map(result => result.ancestors).forEach(ancestors => {
      let branch = tree;
      ancestors.forEach(ancestor => {
        if (!(ancestor.id in branch)) {
          branch[ancestor.id] = {};
        }
        branch = branch[ancestor.id];
      });
    });
    return tree;
  }

  private getAllChildrenMetaData(parentNode) {
    return this.getChildrenMetaData(parentNode.documentId, 0).flatMap((firstMetaData: any) => {

      parentNode.children = firstMetaData.results;

      const observables = [];
      const additionalPagesObs = this.additionalPages(1, firstMetaData.totalPages)
        .reduce((obs, currentPage) => {
          const nextPageObs = this.getChildrenMetaData(parentNode.documentId, currentPage)
            .pipe(tap((nextPage: any) =>
              parentNode.children = (parentNode.children || []).concat(nextPage.results)));
          observables.push(nextPageObs);
          return concat(obs, nextPageObs);
        }, of(null));

      const descendantsMetaDataObsWrapper = Observable.create((observer) => {
        (parentNode.children || [])
          .filter(child => isNonEmptyString(child.id))
          .forEach((currentChild, currentIndex) => {
          const contentObs = this.getEADContent(currentChild.id)
            .pipe(tap(content => {
              parentNode.children[currentIndex] = {
                ...content,
                ...currentChild
              };
            }));
          observer.next(contentObs);
        });
        observer.complete();
      }).pipe(combineAll());

      return concat(additionalPagesObs, descendantsMetaDataObsWrapper);
    });
  }

  private getChildrenMetaData(documentId: string, page: number) {
    const resultsPerPage = 50;

    return this.http.post(`${environment.deprecatedServerUrl}search/children/${documentId}`, {
      resultsPerPage: resultsPerPage,
      startIndex: page * resultsPerPage
    });
  }

  getChildrenOfEADDocument(documentId: string): Observable<any[]> {
    const root: any = {documentId: documentId};
    return this.getAllChildrenMetaData(root)
      .pipe(last())
      .pipe(map(() => {
        return root.children;
      }));
  }

  private additionalPages(firstPage: number, totalPages: number): number[] {
    const pages = [];
    for (let i = firstPage; i < totalPages; i++) {
      pages.push(i);
    }
    return pages;
  }
}
