import { HighlightsNavigator } from '../../archives-tab-hub/highlights-navigator';
import { nextFrame } from '../nextFrame';

export interface HighlightOptions {

  backgroundColor: string;
  activeBackgroundColor: string;
}

export class DescriptionHighlightsNavigator implements HighlightsNavigator {

  private index = -1;
  private selectedElement: HTMLElement;

  readonly hits: number;
  cursor: number;
  hasNextHighlight: boolean;
  hasPreviousHighlight: boolean;

  constructor(private readonly parentNode: HTMLElement,
              private readonly highlights: HTMLElement[],
              private readonly options: HighlightOptions) {
    this.hits = highlights.length;
    this.cursor = 0;
    this.checkHasNextOrPrevious();
  }

  checkHasNextOrPrevious() {
    this.hasNextHighlight = !!this.highlights[this.index + 1];
    this.hasPreviousHighlight = !!this.highlights[this.index - 1];
  }

  nextHighlight<T>(): T {
    if (this.hasNextHighlight) {
      const next = this.highlights[++this.index];
      this.checkHasNextOrPrevious();
      this.cursor++;
      nextFrame(() => {
        this.scrollTo(next);
      });
    }
    return;
  }


  previousHighlight<T>(): T {
    if (this.hasPreviousHighlight) {
      const previous = this.highlights[--this.index];
      this.checkHasNextOrPrevious();
      this.cursor--;
      nextFrame(() => {
        this.scrollTo(previous);
      });
    }
    return;
  }

  private scrollTo(element: HTMLElement) {
    if (this.selectedElement) {
      this.selectedElement.style.backgroundColor = this.options.backgroundColor;
    }
    this.selectedElement = element;

    element.style.backgroundColor = this.options.activeBackgroundColor;
    this.parentNode.scrollTop = element.getBoundingClientRect().top
      - this.parentNode.getBoundingClientRect().top
      + this.parentNode.scrollTop
      - 50;
  }
}
