import { Injectable } from '@angular/core';
import { Location } from '@angular/common';
import { PublicWebcomponentApi } from '../shared.models/public-webcomponent-api';

@Injectable({
  providedIn: 'root'
})
export class ClientConfigurationService {

  private sourceDirectory = '/gaf/zvt';
  private clientApiConfigurator: Function;
  private webcomponentApiResolver = {
    done: (callback) => {
      this.clientApiConfigurator = callback;
    }
  };

  constructor() {
    const initFunction = window['initializeWebcomponent'];

    if (typeof initFunction === 'function') {
      initFunction((config) => {
        this.sourceDirectory = config.sourceDirectory;
        this.clientApiConfigurator = config.configurator || function() {};
        return this.webcomponentApiResolver;
      });
    }
  }

  asset(path: string): string {
    const assetsDirectory = Location.joinWithSlash(this.sourceDirectory, 'assets');
    return Location.joinWithSlash(assetsDirectory, path);
  }

  submitWebcomponentApi(publicWebcomponentApi: PublicWebcomponentApi) {
    this.clientApiConfigurator(publicWebcomponentApi);
  }
}
