import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { EADDocument } from '../../archives-tab.models/ead-document';

@Component({
  selector: 'app-search-content',
  templateUrl: './ead-description.component.html',
  styleUrls: ['./ead-description.component.scss']
})
export class EadDescriptionComponent {

  @Input() eadDocument: EADDocument;
  @ViewChild('eadDescriptionContainer')
  eadDescriptionContainer: ElementRef<HTMLDivElement>;

  panelsState = {
    panel1: true,
    panel2: false,
    panel3: false,
    panel4: false
  };

  expandAllPanels() {
    this.panelsState.panel1 = true;
    this.panelsState.panel2 = true;
    this.panelsState.panel3 = true;
    this.panelsState.panel4 = true;
  }

  togglePanel($event, panel: string) {
    $event.preventDefault();
    this.panelsState[panel] = !this.panelsState[panel];
  }
}
