import { Route } from '@angular/router';
import { Tab } from './tab';
import { PublicTab } from './public-tab';
import { EventSource } from './event-source';
import { TabEntry } from './tab-entry';

export class WebcomponentApi extends EventSource {

  private readonly tabs: Tab[] = [];
  private visibleTabs: Tab[] = [];

  constructor(coreRoutes: Route[]) {
    super();
    coreRoutes.forEach((coreRoute) => {
      this.tabs.push(new Tab(this, coreRoute));
    });
  }

  addTab(options: any): PublicTab {
    const tab = new Tab(this, {
      path: options.id,
      component: null,
      data: {
        ...options,
        key: options.id,
        showTabNavigation: true,
        isCustom: true,
        containerElement: document.createElement('div')
      }
    });

    this.tabs.push(tab);
    return new PublicTab(tab);
  }

  private getDefaultRoute(): Route {
    const activeTabs = this.tabs.filter(tab => tab.isActive());
    const redirectTo = activeTabs.length === 1 ? activeTabs[0].getRoute().path : 'archives';

    return {
      path: '',
      redirectTo: redirectTo,
      pathMatch: 'full'
    };
  }

  getRoutes(): Route[] {
    const routes = this.tabs.map(tab => tab.getRoute());
    routes.push(this.getDefaultRoute());
    return routes;
  }

  getTabs(): Route[] {
    return this.visibleTabs.map(tab => tab.getRoute());
  }

  notifyTabs(eventType: string, args: any[] = []) {
    this.tabs.forEach(tab => tab.notify(eventType, args));
  }

  findTab(tabId: string) {
    const matches = this.tabs.filter(tab => tab.getId() === tabId);
    return matches.length === 1 ? matches[0] : null;
  }

  private setActiveTab(targetTab: Tab) {
    this.tabs.forEach(tab => tab.setActive(false));
    targetTab.setActive(true);
  }

  display(tabEntries: TabEntry[]) {
    this.visibleTabs = [];
    tabEntries.forEach(tabEntry => {
      this.tabs.forEach(tab => {
        if (tab.getId() === tabEntry.id) {
          if (tabEntry.isActive) {
            this.setActiveTab(tab);
          }
          this.visibleTabs.push(tab);
        }
      });
    });
  }

  detectChanges() {
    this.notify('detectChanges');
  }
}

