import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { NavigationService } from '../../shared/services/navigation.service';
import { TabIds } from '../../shared/components/router-outlet/tab-ids';
import { EADDocument } from '../archives-tab.models/ead-document';
import { ArchivesTabService } from '../archives-tab.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from '../../../../node_modules/rxjs/Subscription';

@Component({
  selector: 'app-search-result-descendants',
  templateUrl: './search-result-descendants.component.html',
  styleUrls: ['./search-result-descendants.component.scss']
})
export class SearchResultDescendantsComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  private _totalPages = 0;
  private page = 0;

  document: EADDocument;
  hasPreviousPage = false;
  hasNextPage = false;
  private currentSearchTerm: string;


  constructor(private readonly archivesTabSvc: ArchivesTabService,
              private readonly router: Router,
              private activatedRoute: ActivatedRoute,
              private readonly navigationSvc: NavigationService) {

        this.activatedRoute.queryParams.subscribe(params => {
            this.currentSearchTerm = params['searchTerm'];
        });
  }

  @Input()
  set eadDocument(eadDocument: EADDocument) {
    this.document = eadDocument;
    this._totalPages = eadDocument.totalComponentPages;
    this.checkPreviousNextAvailability();
  }

  ngOnInit() {
    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.page = +queryParams.descendantsPage || 1;
      this.checkPreviousNextAvailability();
    }, TabIds.ARCHIVES);
  }

  private checkPreviousNextAvailability() {
    this.hasPreviousPage = this.page > 1;
    this.hasNextPage = this.page < this._totalPages;
  }

  private goToPage(page: number) {
    this.document.descendantsVisible = false;
    this.archivesTabSvc.loadDescendants(this.document, page);
  }

  goToPreviousPage(event) {
    event.preventDefault();
    this.goToPage(this.page - 1);
  }

  goToNextPage(event) {
    event.preventDefault();
    this.goToPage(this.page + 1);
  }

  hubArchiveDetail(result) {
    //event.preventDefault();
    //this.router.navigate(
    return [
        '/archieven-hub',
        result.repositoryCode,
        'toegang',
        result.findingAidNo,
        'invnr',
        result.unitId,
    ];
    //{queryParams: {searchTerm: this.currentSearchTerm}}
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
