import { TreeNode } from './tree-node';
import { nextFrame } from '../../shared/nextFrame';
import { nextId } from '../next-id';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';

export class TreeModel {

  readonly id = nextId();
  anyNodesOpen = false;
  isNavigationTree = false;

  private _nodes: TreeNode[];
  private allNodes: { [cLevel: string]: TreeNode } = {};
  private activeNode: TreeNode;
  private readonly click: Subject<TreeNode> = new Subject();
  private readonly subscriptions: Subscription[] = [];

  private static expandRecursively(node: TreeNode) {
    if (node) {
      node.data.isExpanded = true;
      this.expandRecursively(node.data.parent);
    }
  }

  set nodes(nodes: TreeNode[]) {
    this.initializeNodesRecursively(null, nodes);
    this._nodes = nodes;
    this.checkAnyNodesExpanded();
  }

  bestMatch(pathToNode: string[]): TreeNode {
    for (let i = pathToNode.length - 1; i >= 0; i--) {
      const node = this.allNodes[pathToNode[i]];
      if (node) {
        return node;
      }
    }
    return null;
  }

  private initializeNodesRecursively(parent: TreeNode, nodes: TreeNode[]) {
    this.initializeNodes(parent, nodes);
    nodes.filter(node => node.inline).forEach(node => {
      this.initializeNodesRecursively(node, node.inline);
    });
  }

  get nodes() {
    return this._nodes;
  }

  setActive(nodeId: string) {
    const node = this.allNodes[nodeId];
    node.data.isActive = true;
    if (this.activeNode && this.activeNode !== node) {
      this.activeNode.data.isActive = false;
    }
    this.activeNode = node;

    TreeModel.expandRecursively(node.data.parent);
    this.checkAnyNodesExpanded();
    nextFrame(() => {
      this.scrollIntoView(node.data.id);
    });

    return {
      expand: () => {
        node.data.isExpanded = true;
      }
    };
  }

  private scrollIntoView(nodeId: string) {
    const nodeElement: Element = document.getElementById(nodeId);
    const scrollContainer: Element = this.findScrollContainerOf(nodeElement);
    scrollContainer.scrollTop = nodeElement.getBoundingClientRect().top
      - scrollContainer.getBoundingClientRect().top
      + scrollContainer.scrollTop
      - 100;
  }

  private findScrollContainerOf(element: Element) {
    if (element === null) {
      return document.documentElement;
    } else if (element.className.indexOf('scroll-container') >= 0) {
      return element;
    } else {
      return this.findScrollContainerOf(element.parentNode as Element);
    }
  }

  initializeNodes(parent: TreeNode, nodes: TreeNode[]) {
    nodes.forEach(node => {
      node.data = {
        id: this.createId(node),
        isExpanded: false,
        isActive: false,
        parent: parent,
        treeModel: this
      };
      this.allNodes[node.cLevel] = node;
    });
  }
  private createId(node: TreeNode): string {
    return `${this.id}-${node.sortKey}`;

  }

  collapseAllNodes() {
    Object.keys(this.allNodes).forEach(key => this.allNodes[key].data.isExpanded = false);
    this.anyNodesOpen = false;
  }

  pathTo(activeID: string): string[] {
    const path = [activeID];
    let node = this.allNodes[activeID];
    while (node.data.parent) {
      path.push(node.data.parent.cLevel);
      node = node.data.parent;
    }
    return path.reverse();
  }

  onClick(callback: (node: TreeNode) => void) {
    this.subscriptions.push(this.click.subscribe(callback));
  }

  clicked(node: TreeNode) {
    this.checkAnyNodesExpanded();
    setTimeout(() => this.click.next(node));
  }

  private checkAnyNodesExpanded() {
    if (!this.nodes) {
      return;
    }

    this.anyNodesOpen = false;
    for (const node of this.nodes) {
      if (node.data.isExpanded) {
        this.anyNodesOpen = true;
        return;
      }
    }
  }

  destroy() {
    this.subscriptions.forEach(subscription => subscription.unsubscribe());
    this._nodes = null;
    this.allNodes = null;
  }
}
