import {Component, Input} from '@angular/core';
import {NavigationService} from '../../shared/services/navigation.service';
import {ActiveFilter} from '../archives-tab.models/active-filter';

@Component({
  selector: 'app-active-filters',
  templateUrl: './active-filters.component.html',
  styleUrls: ['./active-filters.component.scss']
})
export class ActiveFiltersComponent {

  @Input() tabId: string;

  constructor(private readonly navigationSvc: NavigationService) {
  }

  @Input() activeFilters: ActiveFilter[];

  removeActiveFilter(activeFilter: ActiveFilter) {
    if (!activeFilter.disabled) {
      const queryParams = this.navigationSvc.getQueryParams();
      const idsInGroup = queryParams[activeFilter.groupId].split(',');
      const newIdsInGroup = idsInGroup.filter(id => id !== activeFilter.id).join(',');
      this.navigationSvc.updateQueryParams({
        queryParams: {
          [activeFilter.groupId]: newIdsInGroup || null,
          [activeFilter.groupId + 'Label']: null,
          [NavigationService.RESET_ALL('page')]: null,
          [NavigationService.RESET_ALL('startIndex')]: null
        },
        prefix: this.tabId
      });
    }
  }
}
