import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { EADDocumentsResponse } from '../archives-tab.models/ead-documents-response';
import { ApiService } from '../../shared/services/api.service';
import { SharedDataService } from '../../shared/services/sharedData.service';
import { EADDocument } from '../archives-tab.models/ead-document';
import { ArchivesTabService } from '../archives-tab.service';
import { Filter } from '../search-filter/filter';
import { TabIds } from '../../shared/components/router-outlet/tab-ids';
import { NavigationService } from '../../shared/services/navigation.service';
import { RoutingService } from '../../shared/services/routing.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-archives-search-results',
  templateUrl: './search-results.component.html',
  styleUrls: ['../archives-tab.css', './search-results.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SearchResultsComponent implements OnInit, OnDestroy {

  private queryParamsChanged$: Subscription;
  readonly tabId = TabIds.ARCHIVES;

  pageTitle: string;
  searchTerm: string;
  filter: Filter;
  eadDocuments: EADDocumentsResponse;
  totalResults: number;
  currentSearchTerm: string;

  private routeData$: Subscription;

  constructor(private readonly apiSvc: ApiService,
              private readonly sharedDataSvc: SharedDataService,
              private readonly activatedRoute: ActivatedRoute,
              private readonly navigationSvc: NavigationService,
              private readonly routingService: RoutingService,
              private readonly router: Router,
              private readonly archivesTabSvc: ArchivesTabService) {

    this.queryParamsChanged$ = this.navigationSvc.queryParamsChanged((queryParams) => {
      this.archivesTabSvc.getEADDocuments(queryParams);
    }, this.tabId);
  }

  loadDescendants(document: EADDocument) {
    this.archivesTabSvc.loadDescendants(document, 1);
  }

  ngOnInit() {
    this.routeData$ = this.activatedRoute.data.subscribe(data => this.pageTitle = data.pageTitle);

    this.activatedRoute.queryParams.subscribe(params => {
        this.currentSearchTerm = params['searchTerm'];
    });

    this.archivesTabSvc.eadDocuments().subscribe(response => {
      this.eadDocuments = response;
      this.totalResults = this.eadDocuments.totalResults;
      this.filter = new Filter(this.eadDocuments, this.navigationSvc.getQueryParams(this.tabId), this.tabId);
      console.log(this.filter);
    });
  }

  getFindingAidLink(eadDocument: EADDocument) {
    return [
      '/archieven',
      eadDocument.repositoryCode,
      'toegang',
      eadDocument.findingAidNo
    ];
  }


  hubArchiveDetail(event, document: EADDocument) {
    event.preventDefault();
    this.router.navigate(
        [
        '/archieven-hub',
        document.repositoryCode,
        'toegang',
        document.findingAidNo,
        ],
        {queryParams: {searchTerm: this.currentSearchTerm}}

    );
  }

  ngOnDestroy() {
    this.queryParamsChanged$.unsubscribe();
  }
}
