        'text': 'Bevat archieven van de Heren XVII en de kamer Amsterdam (onderling sterk vermengd), de kamers Zeeland, Delft, Rotterdam, Hoorn en Enkhuizen. Het archief van de Heren XVII en de kamer Amsterdam bestaat voornamelijk uit resoluties, verbalen van het Haags Besogne, brieven naar en overgekomen stukken uit de koloni&#xEB;n (aparte series voor de Kaap en China), en na 1700 boekhoudkundige stukken en monsterrollen en na 1700 scheepssoldijboeken. De briefwisseling binnen Europa, scheepsjournalen, financi&#xEB;le stukken v&#xF3;&#xF3;r 1700, monsterrollen en scheepssoldijboeken v&#xF3;&#xF3;r 1700, en de stukken van de departementen in Amsterdam zijn geheel of voor een groot gedeelte verloren gegaan. Van het archief van de kamer Zeeland zijn ongeveer dezelfde bestanden bewaard gebleven, hierin echter nog enige Europese briefwisseling, doch minder boekhoudkundige stukken. Van de archieven van de overige kamers zijn slechts fragmentarische resoluties, scheepssoldijboeken na 1700 (alle kamers), en boekhouding (vooral Hoorn en Enkhuizen) bewaard. Een deel van de briefwisseling met de koloni&#xEB;n zou te reconstrueren zijn m.b.v. de in het Arsip National in Jakarta bewaarde stukken.'
