              'dataType': 'paragraph',
              'text': 'De VOC kwam in 1602 voort uit een fusie van zes kleine compagnie&#xEB;n. Direct nadat de door de Amsterdamse Compagnie van Verre georganiseerde eerste schipvaart van 1595-1597 de mogelijkheid van de vaart op Azi&#xEB; had aangetoond, werden in Amsterdam, Rotterdam en in Zeeland compagnie&#xEB;n opgericht.   <note><p>Over de voorcompagnie&#xEB;n: R. Bijlsma, \'De archieven van de  compagnie&#xEB;n op Oost-Indi&#xEB;, 1594-1603\', Verslagen omtrent \'s Rijks Oude  Archieven 49 (1926) I, 173-224; Hans de Haan, Moedernegotie en grote vaart. Een  studie over de expansie van het Hollandse handelskapitaal in de 16e en 17e eeuw  (Amsterdam 1977) 79-99; F.S. Gaastra, De geschiedenis van de VOC (Zutphen 1992)  13-29.</p></note>  Kapitaal werd door deze compagnie&#xEB;n steeds voor &#xE9;&#xE9;n expeditie bijeengebracht. Wel was er continu&#xEF;teit in de directie; de leidinggevende kooplieden of bewindhebbers droegen zorg voor elkaar opeenvolgende expedities. Telkens na terugkeer van de schepen uit Indi&#xEB; kregen de investeerders, waaronder behalve de bewindhebbers ook andere aandeelhouders of participanten, het oorspronkelijk ingelegde kapitaal terug, uiteraard vermeerderd met een aandeel in de winst. Deze compagnie&#xEB;n beconcurreerden elkaar fel, wat de winstmarges onder druk zette. Het afnemende rendement dreigde de investeerders af te schrikken en bracht de voortgang van de vaart op Indi&#xEB; in gevaar.'
