import { Injectable } from '@angular/core';

@Injectable()
export class UtilService {

  constructor() {
  }

  /**
   * Test whether passed parameter is array and has array items.
   * @param arrayLike
   */
  static hasArrayItems(arrayLike: any): boolean {
    return (Array.isArray(arrayLike) && !!arrayLike.length);
  }
}
