import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '../../../environments/environment';
import { TabIds } from '../../shared/components/router-outlet/tab-ids';
import { HighlightsNavigator } from '../highlights-navigator';
import { HighlightService } from '../../shared/services/highlight.service';
import { TreeService } from '../tree/tree-service';
import { TreeHighlighter } from '../tree/tree-highlighter';
import { TreeResponse } from '../tree/tree-response';
import { TreeRoot } from '../tree/tree-root/tree-root.component';
import { TreeNode } from '../tree/tree-node';
import * as DescriptionMock from './desc-example-ead';

import { SearchCategory } from '../search-category';
import { ArchiveDescriptionComponent } from '../archive-description/archive-description.component';

interface SelectBoxOption {
  value: string;
  label: string;
}

export interface SearchTerm {
  value: string;
  target: SearchCategory;
}

interface NavigationInfo {
  repository: string;  // hub orgID
  findingAidNo: string; // hub spec
  unitID: string; // hub cLevel path or cLevelID TODO must be inline with hub naming
}

interface Highlighters {

  INVENTORY?: TreeHighlighter;
  ITEM_CODE?: TreeHighlighter;
  DESCRIPTION?: HighlightsNavigator;
}

@Component({
  selector: 'app-archive-details-hub',
  templateUrl: './archive-details.component.html',
  styleUrls: [
    '../archives-tab-hub.component.scss',
    './archive-details.component.scss'
  ]
})
export class ArchiveDetailsComponent implements OnInit {

  readonly searchCategory = SearchCategory;

  readonly tabId = TabIds.ARCHIVE_DETAILS_HUB;

  @ViewChild('description') descriptionComponent: ArchiveDescriptionComponent;
  @ViewChild('navigationTree') navigationTree: TreeRoot;
  @ViewChild('contentTree') contentTree: TreeRoot;

  constructor(
    private readonly treeSvc: TreeService,
    private highlightSvc: HighlightService,
    private http: HttpClient,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {
    this.activatedRoute.queryParamMap.subscribe(params => {
      this.currentSearchTerm.value = params.get('searchTerm');
      if (params.has('path')) {
        this.navigationInfo = ArchiveDetailsComponent.extractNavigationPath(params.get('path'));
        this.activeID = this.navigationInfo.unitID;
      } else {
        this.activatedRoute.url.subscribe(url => {
          this.navigationInfo = ArchiveDetailsComponent.extractNavigationPath(url.join('/'));
          this.activeID = this.navigationInfo.unitID;
        });
      }
    });
  }

  searchResultMessage: string;
  highlighters: Highlighters = {};

  nodesOpen = false;
  navigationInfo: NavigationInfo;
  archDesc = {};
  archDescHeader = {};
  nodes = [];
  activeID = '';
  showDescription = false;
  selectOptions: SelectBoxOption[] = [{
    label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-INVENTORY',
    value: SearchCategory.INVENTORY
  }, {
    label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-DESCRIPTION',
    value: SearchCategory.DESCRIPTION
  }, {
    label: 'PAGE-ARCHIVES-DETAILS.SEARCH-IN-ITEM-CODE',
    value: SearchCategory.ITEM_CODE
  }];

  currentSearchTerm: SearchTerm = { 'target': SearchCategory.INVENTORY, 'value': '' };

  private static extractNavigationPath(path: string): NavigationInfo {
    if (path.startsWith('/')) {
      path = path.slice(1);
    }
    const parts = path.split('/');
    return {
      repository: parts[1],
      findingAidNo: parts[3],
      unitID: parts.slice(5).join('/')
    };
  }

  ngOnInit(): void {
    this.currentSearchTerm.value = this.activatedRoute.snapshot.queryParamMap.get('searchTerm');

    this.configureTreeModels();
    const findingAidNo = this.navigationInfo.findingAidNo;

    const eadArchDescURL = `${environment.serverUrl}/search/v1/tree/${findingAidNo}/desc`;
    this.http.get(eadArchDescURL).subscribe(descRes => {
      this.archDesc = DescriptionMock.description;
      this.archDescHeader = DescriptionMock.description.descriptionGroups.filter((group) => group.type === 'archDescription')[0];
    });

    let navUrl = `${environment.serverUrl}/search/v1/tree/${findingAidNo}`;
    navUrl = navUrl + `?byType=series&byType=subfonds&byType=subseries&byType=otherlevel`;
    this.http.get(navUrl).subscribe((response: TreeResponse) => {
      this.navigationTree.treeModel.nodes = response.tree;
      this.loadInitialContentTree();
    });
  }

  private loadInitialContentTree() {
    let url = `${environment.serverUrl}/search/v1/tree/${this.navigationInfo.findingAidNo}`;
    const hasUnitID = this.navigationInfo.unitID !== '';
    const unitID = hasUnitID ? decodeURIComponent(this.navigationInfo.unitID) : null;

    if (!!this.currentSearchTerm.value) {
      this.searchTree(this.currentSearchTerm.value, unitID);
      return;
    }

    if (hasUnitID) {
      if (this.navigationInfo.unitID.indexOf('~') >= 0) {
        url = `${url}/${encodeURIComponent(unitID)}?fillTree=true`;
      } else {
        url = `${url}?byUnitID=${unitID}`;
      }
    }

    this.http.get(url).subscribe((res: TreeResponse) => {
      this.updateTree(res);
    });
  }

  private configureTreeModels() {
    this.navigationTree.treeModel.isNavigationTree = true;

    this.navigationTree.treeModel.onClick((node: TreeNode) => {
      this.currentSearchTerm.value = '';
      this.setNavigationParams(node.cLevel);

      const encodedNodeId = encodeURIComponent(node.cLevel);
      this.navigationTree.treeModel.setActive(node.cLevel);
      const url = `${environment.serverUrl}/search/v1/tree/${this.navigationInfo.findingAidNo}/${encodedNodeId}?fillTree=true`;
      this.http.get(url).subscribe((res: TreeResponse) => {
        this.updateTree(res);
        this.contentTree.treeModel.setActive(node.cLevel);
      });
    });

    this.contentTree.treeModel.onClick((node: TreeNode) => {
      this.currentSearchTerm.value = '';
      this.setNavigationParams(node.cLevel);

      const pathToNode = this.contentTree.treeModel.pathTo(node.cLevel);
      const match = this.navigationTree.treeModel.bestMatch(pathToNode);
      this.navigationTree.treeModel.setActive(match.cLevel);
      this.contentTree.treeModel.setActive(node.cLevel);
    });
  }

  setNavigationParams(nodeID: string): void {
    this.activeID = nodeID;
    this.navigationInfo.unitID = nodeID;
    this.router.navigate(
      [this.getNavigationPath()],
      {
        relativeTo: this.activatedRoute,
        queryParams: {
          searchTerm: this.currentSearchTerm.value || null
        },
        queryParamsHandling: 'merge'
      });
  }

  getNavigationPath(): String {
    const nav = this.navigationInfo;
    if (nav.unitID === '') {
      return `/archieven-hub/${nav.repository}/toegang/${nav.findingAidNo}`;
    }
    const unitID = encodeURIComponent(nav.unitID);
    return `/archieven-hub/${nav.repository}/toegang/${nav.findingAidNo}/invnr/${encodeURIComponent(unitID)}`;
  }

  collapseAll() {
    this.setNavigationParams('');
    this.navigationTree.treeModel.collapseAllNodes();
    this.contentTree.treeModel.collapseAllNodes();
  }

  performSearch(searchTerm: string) {
    if (this.currentSearchTerm.target === SearchCategory.DESCRIPTION) {
      this.displayDescription();
      this.highlighters[SearchCategory.DESCRIPTION] = this.createDescriptionHighlighter(searchTerm);
    } else {
      if (this.currentSearchTerm.target === SearchCategory.ITEM_CODE) {
        this.currentSearchTerm.value = '';
      }
      this.searchTree(searchTerm, '');
    }
  }

  searchTree(term: string, unitID: string) {
    const target = this.currentSearchTerm.target;
    this.treeSvc.searchTree(this.navigationInfo.findingAidNo, term, unitID, target).subscribe(result => {
      this.updateTree(result.response);

      const treeHighlighter: TreeHighlighter = this.highlighters[target] as TreeHighlighter;
      if (treeHighlighter) {
        treeHighlighter.removeAllSubscriptions();
      }
      this.highlighters[target] = result.highlightsNavigator;
      result.highlightsNavigator.onChange((response: TreeResponse) => {
        this.currentSearchTerm.value = term;
        this.updateTree(response);
      });
    });
    this.highlighters = {};
  }

  updateTree(response: TreeResponse) {
    this.searchResultMessage = '';
    if (!response.tree) {
      this.searchResultMessage = 'No results found';
      this.contentTree.treeModel.nodes = [];
      return;
    }
    this.setNavigationParams(response.treeHeader ? response.treeHeader.activeID : '');
    this.contentTree.treeModel.nodes = response.tree;

    if (this.activeID) {
      this.contentTree.treeModel.setActive(this.activeID).expand();
      const pathToNode = this.contentTree.treeModel.pathTo(this.activeID);
      const match = this.navigationTree.treeModel.bestMatch(pathToNode);
      this.navigationTree.treeModel.setActive(match.cLevel).expand();
    }
  }

  displayDescription() {
    this.showDescription = true;
  }

  hideDescription() {
    this.showDescription = false;
  }

  createDescriptionHighlighter(searchTerm: string): HighlightsNavigator {
    this.descriptionComponent.expandAllPanels();
    return this.highlightSvc.highlight(this.descriptionComponent.containerElement.nativeElement, searchTerm);
  }

  onSearchTargetChanged(newTarget: SearchCategory) {
    this.currentSearchTerm.target = newTarget;
    this.highlighters = {};

    if (newTarget === SearchCategory.DESCRIPTION) {
      this.displayDescription();
    } else {
      this.hideDescription();
    }
  }
}
