import { Component, Input, SecurityContext, ViewEncapsulation } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-data-item-list',
  templateUrl: './data-item-list.component.html',
  styleUrls: ['./data-item-list.component.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class DataItemListComponent {

  @Input() listObj: any;

  static isAcceptedListProperty(passedProperty): boolean {
    const acceptedProperties = ['disc', 'circle', 'square', 'none'];
    return acceptedProperties.includes(passedProperty);
  }

  constructor(private readonly sanitizer: DomSanitizer) {
  }


  getListType(style: string): string {
    return DataItemListComponent.isAcceptedListProperty(style) ?
      this.sanitizer.sanitize(SecurityContext.STYLE, style) : 'circle';
  }

  getListClass(style: string): string {
    return DataItemListComponent.isAcceptedListProperty(style) ? null : style;
  }
}
